import React from 'react'
import { ChkForm } from '../../common/common.style'

const ChkFormComp = ({id,text,onChange,checked}) => {
  return (
    <ChkForm>
      <input type="checkbox" name="" className="check" id={id}  onChange={onChange} checked={checked}  />
      <label htmlFor={id}>{text}</label></ChkForm>
  )
}

export default ChkFormComp