import React from 'react'
import { Link } from 'react-router-dom'
import MainIntro from './MainIntro'
import { MainSec, MainWrap, ServiceStyle } from './main.style'
import { MemorialUl } from '../Memorial/memorial.style'
import MemorialData from '../Memorial/MemorialData'
import DtNoticeData from '../DeathNotice/DtNoticeData'
import IcoSv1 from '../../images/main/ico_service1.png';
import IcoSv2 from '../../images/main/ico_service2.png';
import IcoSv3 from '../../images/main/ico_service3.png';
import IcoSv4 from '../../images/main/ico_service4.png';
import ContList from '../../components/List/ContList'
import { Container, PageTitle, SecDesc } from '../../common/common.style'

const Main = () => {
  //const combinedData = [...DtNoticeData, ...MemorialData].slice(0, 8);
  const combinedData = [
    ...DtNoticeData.map((item) => ({ ...item, source: 'DtNotice' })),
    ...MemorialData.map((item) => ({ ...item, source: 'Memorial' }))
  ].slice(0, 8);
  return (
    <>
      <MainWrap>
        <MainIntro />
        <Container className='main_conainer'>
            <MainSec>
              <PageTitle>추모관</PageTitle>
              <SecDesc>고인의 추모관을 방문해 고인에 대한 그리운 마음과 <br/> 생전에 하지 못한 이야기를 나누세요 </SecDesc> 
              <MemorialUl>
                {combinedData.map((value, index) => (
                  <ContList
                    key={value.id}
                    value={value}
                    type="main"
                    link={`${value.source === 'DtNotice' ? '/DeathNoiceView' : '/MemorialView'}/${value.id}`}
                  />
                ))}
              </MemorialUl>
            </MainSec>
            <MainSec>
              <PageTitle>MEMORIAL CANVAS</PageTitle>
              <SecDesc>메모리얼 캔버스는 고인의 삶과 메시지에 초점을 맞추어
                <br />오롯이 고인을 추모하는 장례문화를 만들어 갑니다.</SecDesc> 
              <ServiceStyle>
                <li className='sv1'> 
                  <Link to='/dt_notice'>
                    <i><img src={IcoSv1} alt="" /></i>
                    <strong>부고</strong>
                    <p className="desc">상주가 부고를 만들어<br/>유족과 지인들에게<br/>알릴 수 있습니다.</p>
                  </Link>
                </li>
                <li className='sv2'> 
                  <Link to='/memorial'>
                    <i><img src={IcoSv2} alt="" /></i>
                    <strong>추모관</strong>
                    <p className="desc">부고이후, 추모관으로 자동 전환되어<br/>지속적으로 고인을<br/>추모할 수 있습니다.</p>
                  </Link>
                </li>
                <li className='sv3'> 
                  <Link to=''>
                    <i><img src={IcoSv3} alt="" /></i>
                    <strong>조문보</strong>
                    <p className="desc">고인의 삶과 정신적 유산을<br/>조문보를 통해 알립니다.</p>
                  </Link>
                </li>
                <li className='sv4'> 
                  <Link to='faq'>
                    <i><img src={IcoSv4} alt="" /></i>
                    <strong>FAQ</strong>
                    <p className="desc">부고 및 추모관, 조문보 서비스에<br/>대한 사항을 확인하실 수<br/>있습니다.</p>
                  </Link>
                </li>
              </ServiceStyle> 
            </MainSec>
        </Container>
      </MainWrap>
    </>
  )
}

export default Main