import styled from 'styled-components';
import IcoKakao from '../../images/ico_kakao_sm.png';
import IcoSms from '../../images/ico_sms_sm.png';
import IcoShare from '../../images/ico_share.png';
import IcoToss from '../../images/ico_toss_sm.png';
import IcoKakaoPay from '../../images/ico_kakaopay_sm.png';

export const PopupWrap = styled.div`
  position:fixed;
  width:100vw;
  height:100vh;
  top:0;
  left:0;
  right:0;
  bottom:0;
  z-index:990;
  
  .dim{
    width:100vw;
    height:100vh;
    background-color:rgba(0, 0, 0, 0.7);
  }
  .bottom_btn{
    margin-top:3rem;
  }
`
export const Popupcontent = styled.div`
  position:fixed;
  top:50%;
  left:50%;
  transform:translate(-50%, -50%);
  width:800px;
  margin:0 auto;
  box-sizing:border-box;  
  overflow:hidden;
  @media (max-width: 768px) {
    width:calc(100% - 4rem);
  }
  .inner{
    position:relative;
    padding:4rem 2rem;
    background:#fff;
  }
  .desc{
    margin:2rem 0;
  }
`

export const BtnSendStyle = styled.button`
  width:100%;
  height:6.5rem;
  position:relative;
  display:flex;
  margin-bottom:1rem;
  align-items:center;
  padding:2rem;
  font-size:1.6rem;
  border-radius:1rem;
  border:1px solid var(--form-border);
  box-sizing:border-box;
  &:before{
    content:"";
    display:inline-block;
    margin-right:1rem;
    background-repeat:no-repeat;
    background-size:cover;
  }
  &.kakao:before{
    width:2.5rem;
    height:2.2rem;
    background-image:url(${IcoKakao});
  }
  &.sms:before{
    width:2.2rem;
    height:2.2rem;
    background-image:url(${IcoSms});
  }
  &.share:before{
    width:2.8rem;
    height:1.8rem;
    background-image:url(${IcoShare});
  }
  &.toss:before{
    width:6.4rem;
    height:1.8rem;
    background-image:url(${IcoToss});
  }
  &.kakaopay:before{
    width:5.4rem;
    height:2.4rem;
    background-image:url(${IcoKakaoPay});
  }
    
  &:after{
    content:"";
    position:absolute;
    top:50%;
    transform:translateY(-50%) rotate(-135deg);
    right:24px;
    width:1rem;
    height:1rem;
    border-left:1px solid #666;
    border-bottom:1px solid #666
  
  }
  
`
export const QrBox = styled.div`
  width:100%;
  margin-bottom:1rem;
  align-items:center;
  padding:2rem;
  font-size:1.6rem;
  border-radius:1rem;
  border:1px solid var(--form-border);
  box-sizing:border-box;
  &:before,&:after{
    display:none;
  }
  strong{
    display:block;
    margin-bottom:1rem;
    font-weight:normal;
  }
`
export const QrItems = styled.div`
  display:flex;
  gap:4rem;
  margin-top:2rem;
  .item{
    text-align:center;
    .ico{
      display:inline-block;
      height:1.8rem;
    }
    .qr{
      width:13.8rem;
      height:14rem;
    }
    img{
      object-fit:cover;
    }
  }
  @media (max-width: 768px) {
    display:flex;
    justify-content:center;
  }
  
`

export const SendBoxWrap = styled.div`
  margin-top:3rem;
  .chk_wrap{
    padding-bottom:2rem;
    border-bottom:1px solid var(--form-border);
  }
`

export const SendBox = styled.div`
  &.image_type{
    width:30rem;
    margin:2rem auto;
    overflow:hidden;
    border:1px solid var(--form-border);
    border-radius:3rem;
    .txt_box{
     
    }
  }
  h3{
    margin:2rem 0;
    font-size:1.6rem;
  }
  .desc{
    margin:2rem 0;
    font-size:1.4rem;
    color:var(--gray-color);
    word-break:keep-all;
  }
  
`
export const MajorImg = styled.div`
  width:100%;
  height: 24rem;
  img{
    object-fit:cover;
    width:100%;
    height:100%;
  }
`

export const SendTxtBoxStyle =styled.div`
  padding:2rem;
  font-size:1.4rem;
  line-height:var(--txt-lh);
  height:14rem;
  overflow-y:auto;

  border: ${(props) => props.$border && '1px solid var(--form-border)'};
  em{
    display:block;
  }
  ul{
    margin-top:2rem;
  }
`
export const BtnSendWrap = styled.div`
  margin:3rem 0;
`
export const SendMoneyTit =styled.div`
  .top{
    margin:4rem 0 1rem;
    *{
      font-size:1.4rem;
      color:#0C1622;
      font-weight:400;
    }
    .btn_copy{
      text-decoration:underline;
    }
  }
  .account{
    font-size:1.8rem;
    line-height:var(--txt-lh);
    font-weight:500;
  }
 

`