import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom';
import { FormWrap, Desc, Grid2Col, Valid, Row, Label, FileUpload, GridStick, ToggleBtn } from '../../components/Form/form.style';
import { FileInput } from '../../components/Form/FileInput';
import CondolenceForm from '../../components/Form/CondolenceForm';
import { BtnWrap, Button, Container, FlexSpace, LineBtn, PageTitle, SecDesc, Section, SecTitle } from '../../common/common.style';
import { CondolenceFormStyle } from '../../components/List/contList.style';
import Req from '../../components/Form/Req';

function MemorialRegister() {
  const [imgFile, setImgFile] = useState(null);
  const [condolenceRows, setCondolenceRows] = useState([{ id: 0 }]);
  const [isOpenActive, setIsOpenActive] = useState(true);

  const handleFileImgChange = (e) => {
    e.preventDefault();
    const file = e.target.files[0]; // 입력된 파일 가져오기
    setImgFile(file); // 파일을 상태에 저장
  };
  //조문보 관련 함수들
  const addCondolenceRow = (e) => {
    e.preventDefault();
    setCondolenceRows([...condolenceRows, { id: condolenceRows.length }]);
  };
  const removeCondolenceRow = (id) => {
    setCondolenceRows((prevRows) => prevRows.filter((row) => row.id !== id));
  };
  const moveCondolenceRowDown = (index) => {
    if (index >= condolenceRows.length - 1) return; // 마지막 항목이면 실행하지 않음
    const updatedRows = [...condolenceRows];
    const temp = updatedRows[index + 1];
    updatedRows[index + 1] = updatedRows[index];
    updatedRows[index] = temp;
    setCondolenceRows(updatedRows); // 상태 업데이트
    
  };
  const moveCondolenceRowUp = (index) => {
    if (index === 0) return;
    const updatedRows = [...condolenceRows];
    const temp = updatedRows[index - 1];
    updatedRows[index - 1] = updatedRows[index];
    updatedRows[index] = temp;
    setCondolenceRows(updatedRows);
  };

  const openOptClick = () => {
    setIsOpenActive(!isOpenActive); 
  };
  const navigate = useNavigate();
  const handleCancel = () => {
    navigate(-1); // 이전 페이지로 이동
  };

  return (
    
  <form action='#'>
    <Container className='wide'>
      <PageTitle>추모관 등록</PageTitle>
      <FormWrap>
        <div className='form'>
          <Section>
            <FlexSpace>
              <SecTitle>고인 정보</SecTitle>
              <Desc>*표시는 필수 입력값입니다.</Desc>
            </FlexSpace>
            <Row>
              <Grid2Col>  
                <div>
                  <Label htmlFor="name">성함<Req /></Label>
                  <input type="text" id="name" placeholder="고인의 성함을 입력하세요" />
                </div>
                <div>
                  <Label htmlFor="position">직위</Label>
                  <input type='text' id='position' placeholder="ex)박사,교수,회장" />
                </div>
              </Grid2Col>
              <Valid>성함을 입력하세요</Valid>
            </Row>
            <Row>
              <Label htmlFor="birth">생년월일</Label>
              <input type='date' id='birth' />
            </Row>
            <Row>
              <Label htmlFor="deathDate">임종일자</Label>
              <input type='date' id='deathDate' />
            </Row>
            <Row>
                <Label>영정사진</Label>
                <FileInput value={imgFile} onChange={handleFileImgChange}/>
            </Row>
          </Section>
          <Section>
            <SecTitle>추모영상</SecTitle>
            <Row>
              <FileUpload $video>
                <label htmlFor="fuleUpload" className="file">파일선택
                <input type="file" id="fuleUpload"/></label>
              </FileUpload>
            </Row>
          </Section>
          <Section>
            <SecTitle className='sec_title blind'>추모 정보 입력 폼</SecTitle>
            <Row>
              <Label htmlFor="symbolSlogan">상징문구</Label>
              <input type='text' id='symbolSlogan' placeholder='고인을 상징할 수 있는 문구(예: 명랑소녀 OOO여사)' />
            </Row>
            <Row>
              <Label htmlFor="note">안내글<Req /></Label>
              <input type='text' id='note' placeholder='예)저희 상가는 조의금을 정중히 사양합니다.' />
            </Row>
            <Row>
              <Label htmlFor="memorialAs">추도사<Req /></Label>
              <textarea id="memorialAs" placeholder="추도사를 작성하세요." />
            </Row>
          </Section>
          <Section>
              <SecTitle>조문보 등록</SecTitle>
              <div className='reg_desc'>조문보 등록은 최대 5개까지 등록할 수 있습니다.</div>
              <Row>
                <CondolenceFormStyle>
                  <Label htmlFor="condolenceNote">조문보</Label>
                  <GridStick className='w_half'>  
                    <select id="condolenceNote">
                      <option>고인관계</option>
                      <option>부</option>
                      <option>모</option>
                      <option>아들</option>
                      <option>딸</option>
                    </select>
                    <input type='text' id="" className='stick'/>
                  </GridStick>
                  <textarea id="" placeholder="" />
                </CondolenceFormStyle>
              </Row>
              <Row>
                {
                  condolenceRows.map((row,index) => (
                    <Row key={row.id}>
                      <CondolenceForm 
                        id={row.id}
                        onDelete={() => removeCondolenceRow(row.id)}
                        onMoveUp={() => moveCondolenceRowUp(index)}
                        onMoveDown={() => moveCondolenceRowDown(index)}
                      />
                    </Row>
                  ))

                }
              </Row>
              <LineBtn className='center' onClick={addCondolenceRow}>조문보 추가</LineBtn>
            </Section>
            <Section>
              <FlexSpace>
                <SecTitle>부고 공개 여부</SecTitle>
                <ToggleBtn>
                  <span className='txt'>{isOpenActive ? "공개" : "비공개"}</span> <span className={`switch ${isOpenActive ? "active" : ""}`} onClick={openOptClick}></span>
                </ToggleBtn>
              </FlexSpace>
              <SecDesc col_margin="2rem">비공개로 등록하더라도 추후에 공개로 수정하여 등록하고 공유할 수 있습니다.</SecDesc>
            </Section>
            <BtnWrap>
              <LineBtn onClick={handleCancel}>취소</LineBtn>
              <Button $primaryBtn>등록완료</Button>
            </BtnWrap>
        </div>
      </FormWrap>
    </Container>
  </form>
  )
}

export default MemorialRegister