import styled from 'styled-components';
import LogoImg from '../../images/logo_dark.png';
import LogoImg_w from '../../images/logo.png';
import IcoMember from '../../images/ico_member.png';
import IcoMemberW from '../../images/ico_member_w.png';

export const HeaderWrap=styled.header`
  background: ${props => props.$actBg ?  '#fff' : 'var(--primary-color)'};
  transition: .4s;

  .container{
    position:relative;
    margin:0 auto;
  }
  .left{
    align-items:center;
    gap:60px;
  }
  &.fixed{
    position:fixed;
    top:0;
    left:0;
    right:0;
    z-index:900;
    &.border_bt{
      border-bottom:1px solid #000
    }
  }
  
  @media (max-width: 1024px) {
    .container{
      height:6rem;
    }
  }
`  

export const Logo = styled.h1`
  display:block;
  width:116px;
  height:46px;
  position:absolute;
  top:50%;
  left:20px;
  transform:translateY(-50%);
  background-image:url(${props => props.$actType ?  LogoImg : LogoImg_w});
  background-repeat:no-repeat;
  background-size:contain;
  a{
    display:block;
    width:100%;
    height:100%;
  }
  @media (max-width: 1024px) {
    height:34px;
    background-size:80%;
  }
  
`

export const MenuWrap =styled.div`
    width : calc(100% - 160px);
    margin-left:160px;
    display:flex;
    justify-content:space-between;
    /*모바일 메뉴*/
    @media (max-width: 1024px) {
      width:100%;
      height:96vh;
      position:absolute;
      top: 6rem;
      right:-100%;
      transition:all .5s ease;
      text-align:center;
      background:#fff;
      flex-direction:column-reverse;
      justify-content:flex-end;
      z-index:900;
      li a{
        color:var(--primary-color) !important;
      }
      &.open{
        right:0;
      }
      .nav_area{
        flex-direction:column;
        gap:2rem !important;
      }
      .login_area{
        margin-right:4rem;
        justify-content:flex-end;
      }  
    }
`
export const HeadList = styled.ul`
  &.flex{
    gap:4rem;
    align-items:center;
    li{
      line-height:80px;
      a{
        font-size:2rem;
        color:${props => props.$act_type ? 'var(--primary-color)' : '#fff'};
        &:hover{
          text-decoration:underline;
        } 
      }
    }
  }
  &.login_area{
   gap:2rem;
   li a{
    font-size:1.8rem;
   }
  } 

`
export const MypageLink = styled.li`
  width:2.2rem;
  height:2.2rem;
  background:url(${props => props.$act_type ? IcoMember : IcoMemberW}) no-repeat;
  background-size:cover;
  a{
    display:block;
    width:100%;
    height:100%;
  }
   
`
export const MbBtn = styled.button`
  display:none;
  @media (max-width: 1024px) {
    display:block;
    position:absolute;
    top:50%;
    right:20px;
    transform:translateY(-50%);
    &:before,
    &:after{
      content:"";
      display:block;
      background:${props => props.$actType ? 'var(--primary-color)' : '#fff'};
      width:30px;
      height:3px;
      transition: .4s;
    }
    &:after{
      margin-top:4px;
    }

    &.close{
      &:before{
        transform:rotate(45deg);
      }
      &:after{
        transform:translateY(-7px) rotate(-45deg);
      }
    }
  }
`

;