import React from 'react'
import { useNavigate } from 'react-router-dom';
import { MemorialUl } from './memorial.style'
import { Container, FillBtn, Flex, FlexSpace, MoreBtn, PageTitle, SecDesc } from '../../common/common.style'
import MemorialData from "./MemorialData"
import SearchForm from '../../components/Form/SearchForm';
import ContList from '../../components/List/ContList';
const Memorial = () => {
  const navigate = useNavigate();
  const linkpath = (path) => {
    navigate(path);
  };

  return (
    <>
      <Container>
        <PageTitle>추모관</PageTitle>
        <FlexSpace className='mb_column'>
          <SecDesc>고인의 추모관을 방문해 고인에 대한 그리운 마음과 <br/> 생전에 하지 못한 이야기를 나누세요 </SecDesc> 
          <Flex>
            <SearchForm />
            <FillBtn $primaryBtn onClick={() => linkpath("/memorialReg")}>추모관 등록</FillBtn>  
          </Flex>
        </FlexSpace>
        <MemorialUl>
          {
              MemorialData.map((value,index) =>{
                return <ContList
                  key={value.id}
                  value={value}
                  link={`/memorialView/${value.id}`}
                />
                
              })
          }
        </MemorialUl>
        <MoreBtn>더보기</MoreBtn>
      </Container>
    </>
  )
}

export default Memorial