import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom';
import { FormWrap, Desc, Grid2Col, Valid, Row, Label, FileUpload, CheckForm, GridStick, FuneralSch, Grid3ColStick, ToggleBtn } from '../../components/Form/form.style'
import { BtnWrap, Button,  Container,  Flex,  FlexSpace,  LineBtn, PageTitle, SecDesc, Section, SecTitle} from '../../common/common.style';
import FmMemberForm from '../../components/Form/FmMemberForm'
import AccountForm from '../../components/Form/AccountForm'
import HistoryForm from '../../components/Form/HistoryForm'
import { FileInput } from '../../components/Form/FileInput'
import ChkFormComp from '../../components/Form/ChkFormComp';
import Req from '../../components/Form/Req';


const DtNoticeRegister = () => {
  const [allValue, setAllValue] = useState([])
  const [majorNm,setMajorNm] = useState('')
  const [position,setPosition] = useState('')
  const [fmMemberRows, setfmMemberRows] = useState([{ id: 0 }]);
  const [accountRows, setAccountRows] = useState([{ id: 0 }]);
  const [historyRows, setHistoryRows] = useState([{ id: 0 }]);
  const [isActive, setIsActive] = useState(true);
  const [isOpenActive, setIsOpenActive] = useState(true);
  const [imgFile, setImgFile] = useState(null);

  // 상주 정보에 대한 함수들
  const updateFmMemberRow = (id, key, value) => {
    setfmMemberRows((prevRows) => 
      prevRows.map((row) =>
        row.id === id ? { ...row, [key]: value } : row
      )
    );
  };
  const addFmemberRow = (e) => {
    e.preventDefault();
    setfmMemberRows([...fmMemberRows, { id: fmMemberRows.length }]);
  };
  const removeFmMemberRow = (id) => {
    //setfmMemberRows(fmMemberRows.filter((row) => row.id !== id));
    setfmMemberRows((prevRows) => prevRows.filter((row) => row.id !== id));
  };

  const moveFmMemberRowDown = (index) => {
    if (index >= fmMemberRows.length - 1) return; // 마지막 항목이면 실행하지 않음

    const updatedRows = [...fmMemberRows];
    const temp = updatedRows[index + 1];
    updatedRows[index + 1] = updatedRows[index];
    updatedRows[index] = temp;

    setfmMemberRows(updatedRows); // 상태 업데이트
    
  };
  const moveFmMemberRowUp = (index) => {
    if (index === 0) return;
    const updatedRows = [...fmMemberRows];
    const temp = updatedRows[index - 1];
    updatedRows[index - 1] = updatedRows[index];
    updatedRows[index] = temp;
    setfmMemberRows(updatedRows);
  };

  //계좌등록 함수들
  const addAccountRow = (e) => {
    e.preventDefault();
    setAccountRows([...accountRows, { id: accountRows.length }]);
  };
  const removeAccountRow = (id) => {
    setAccountRows((prevRows) => prevRows.filter((row) => row.id !== id));
  };
  const moveAccountRowDown = (index) => {
    if (index >= accountRows.length - 1) return; // 마지막 항목이면 실행하지 않음
    const updatedRows = [...accountRows];
    const temp = updatedRows[index + 1];
    updatedRows[index + 1] = updatedRows[index];
    updatedRows[index] = temp;
    setAccountRows(updatedRows); // 상태 업데이트
    
  };
  const moveAccountRowUp = (index) => {
    if (index === 0) return;
    const updatedRows = [...accountRows];
    const temp = updatedRows[index - 1];
    updatedRows[index - 1] = updatedRows[index];
    updatedRows[index] = temp;
    setAccountRows(updatedRows);
  };
  //일대기 관련 함수들
  const addHistoryRow = (e) => {
    e.preventDefault();
    setHistoryRows([...historyRows, { id: historyRows.length }]);
  };
  const removeHistoryRow = (id) => {
    setHistoryRows((prevRows) => prevRows.filter((row) => row.id !== id));
  };
  const moveHistoryRowDown = (index) => {
    if (index >= historyRows.length - 1) return; // 마지막 항목이면 실행하지 않음
    const updatedRows = [...historyRows];
    const temp = updatedRows[index + 1];
    updatedRows[index + 1] = updatedRows[index];
    updatedRows[index] = temp;
    setHistoryRows(updatedRows); // 상태 업데이트
    
  };
  const moveHistoryRowUp = (index) => {
    if (index === 0) return;
    const updatedRows = [...historyRows];
    const temp = updatedRows[index - 1];
    updatedRows[index - 1] = updatedRows[index];
    updatedRows[index] = temp;
    setHistoryRows(updatedRows);
  };

  const handleClick = () => {
    setIsActive(!isActive); 
  };
  const openOptClick = () => {
    setIsOpenActive(!isOpenActive); 
  };

  const handleFileImgChange = (e) => {
    e.preventDefault(); 
    const file = e.target.files[0]; // 입력된 파일 가져오기
    setImgFile(file); // 파일을 상태에 저장
  };
  
  //submit
  const formSubmit = (e) =>{
    e.preventDefault();
    const fmMembers = fmMemberRows.map((row) => ({
      fmMemberSel: row.fmMemberSel || "",
      fmMemberNm: row.fmMemberNm || "",
    }));

    const newEntry = {
      majorNm,
      position,
      fmMembers,
    };

    setAllValue((prev) => [...prev, newEntry]); // 새 데이터를 allValue에 추가
    resetForm(); // 폼 초기화
  }
  const resetForm = () => {
    setMajorNm("");
    setPosition("");
    setfmMemberRows([{ id: 0 }]); // 초기 상태로 리셋
  };
  const navigate = useNavigate();
  const handleCancel = () => {
    navigate(-1); // 이전 페이지로 이동
  };

  // useEffect(() => {
  //   console.log("Updated fmMemberRows:", fmMemberRows);
  // }, [fmMemberRows]); // 상태가 변경될 때마다 실행

  return (
    <>
      <form onSubmit = {formSubmit}>
        <Container className='wide'>
          <PageTitle>부고등록</PageTitle>
          <FormWrap>
              <div className='register form'>
                <Section>
                  <FlexSpace>
                    <SecTitle>고인 정보</SecTitle>
                    <Desc>*표시는 필수 입력값입니다.</Desc>
                  </FlexSpace>
                  <Row>
                    <Grid2Col>  
                      <div>
                        <Label htmlFor="name">성함<Req /></Label>
                        <input type="text" id="name" placeholder="고인의 성함을 입력하세요" name={majorNm} onChange={(e) => setMajorNm(e.target.value)} />
                      </div>
                      <div>
                        <Label htmlFor="position">직위</Label>
                        <input type='text' id='position' placeholder="ex)박사,교수,회장" name={position} onChange={(e) => setPosition(e.target.value)} />
                      </div>
                    </Grid2Col>
                    <Valid>성함을 입력하세요</Valid>
                  </Row>
                  <Row>
                    <Label htmlFor="birth">생년월일</Label>
                    <input type='date' id='birth' />
                  </Row>
                  <Row>
                    <Label htmlFor="deathDate">임종일자</Label>
                    <input type='date' id='deathDate' />
                  </Row>
                  <Row>
                      <Label>영정사진</Label>
                      <FileInput value={imgFile} onChange={handleFileImgChange}/>
                  </Row>
                </Section>
                <Section>
                  <FlexSpace>
                    <SecTitle>장례 정보</SecTitle>
                    <ChkFormComp id="shareChk" text="부고 공유 기능 사용" />
                  </FlexSpace>
                  <h4 className='sub_title ft_ariburi'>상주 정보</h4>
                  <Row>
                    <Label htmlFor="represent">대표상주<Req /></Label>
                      <GridStick>  
                        <select id="represent">
                          <option>고인관계</option>
                          <option>부</option>
                          <option>모</option>
                          <option>아들</option>
                          <option>딸</option>
                        </select>
                        <input type='text' className='stick' placeholder="상주명을 입력하세요" />
                      </GridStick>
                  </Row>
                  {fmMemberRows.map((row, index) => (
                    <Row key={row.id}>
                      <FmMemberForm 
                          id={row.id}
                          fmMemberSel={row.fmMemberSel}
                          fmMemberNm={row.fmMemberNm}
                          onUpdate={updateFmMemberRow}
                          onDelete={() => removeFmMemberRow(row.id)}
                          onMoveUp={() => moveFmMemberRowUp(index)}
                          onMoveDown={() => moveFmMemberRowDown(index)}
                      />
                    </Row>
                  ))}
                  <LineBtn className='center' onClick={addFmemberRow}>상주 추가</LineBtn>
                </Section>
                <Section> 
                  <SecTitle className='blind'>장례 정보</SecTitle>
                  <Row>
                    <Label htmlFor="funeral">빈소(장례식장)<Req /></Label>
                    <Grid2Col>  
                      <FuneralSch>
                        <input type="text" id="funeral" placeholder="장례식장을 검색하세요" />
                        <button>검색</button>
                      </FuneralSch>
                      <div>
                        <input type='text' id='' placeholder="호실" />
                      </div>
                    </Grid2Col>
                  </Row> 
                  <Row>
                    <Label htmlFor="funeralDt">발인일자<Req /></Label>
                    <GridStick className='w_half'>  
                      <input type='date' id="funeralDt"/>
                      <input type='time' className='stick' />
                    </GridStick>
                  </Row>
                  <Row>
                    <Label htmlFor="finalplace">장지</Label>
                    <input type='text' id='finalplace' placeholder="장지 주소를 입력하세요" />
                  </Row>
                </Section>
                <Section>
                  <FlexSpace>
                    <SecTitle>조의금 계좌 등록</SecTitle>
                    <CheckForm>
                        <input type="checkbox" name="" className="check" id="accountShare" />
                        <label htmlFor="accountShare">조의금 계좌 공개</label>
                    </CheckForm>
                  </FlexSpace>
                  <Row>
                    <Label htmlFor="repAct">대표계좌</Label>
                    <Grid3ColStick>  
                      <input type='text' id="repAct" placeholder='계좌명' />
                      <select id="" className='stick' >
                        <option>은행</option>
                        <option>옵션1</option>
                        <option>옵션2</option>
                        <option>옵션3</option>
                      </select >
                      <input type='text' className='stick' placeholder='계좌번호를 입력하세요'/>
                    </Grid3ColStick>
                  </Row>
                    {
                      accountRows.map((row,index) => (
                        <Row key={row.id}>
                          <AccountForm 
                            id={row.id}
                            onDelete={() => removeAccountRow(row.id)}
                            onMoveUp={() => moveAccountRowUp(index)}
                            onMoveDown={() => moveAccountRowDown(index)}
                          />
                        </Row>
                      ))
                    }
                    <LineBtn className='center' onClick={addAccountRow}>계좌 추가</LineBtn>
                </Section>
                <Section>
                  <SecTitle>추모영상</SecTitle>
                  <Row>
                    <FileUpload $video>
                      <label htmlFor="fuleUpload" className="file">파일선택
                      <input type="file" id="fuleUpload"/></label>
                    </FileUpload>
                  </Row>
                </Section>
                <Section>
                  <SecTitle>추도사<Req /></SecTitle>
                  <Row>
                    <textarea id="" placeholder="추도사를 작성하세요." />
                  </Row>
                </Section>
                <Section>
                  <SecTitle>일대기 작성</SecTitle>
                  <div className='reg_desc'>※ 고인의 일대기는 최대 5개까지 등록할 수 있습니다.</div>
                  <Row>
                    <FlexSpace>
                      <Flex>
                        <Label htmlFor="history">
                          일대기<Req />
                        </Label>
                      </Flex>
                    </FlexSpace>
                    <input type='text' id="history" placeholder='제목을 입력하세요(최대 20글자)'/>
                    <textarea placeholder='고인의 일대기를 작성하세요'></textarea>
                    <ToggleBtn>
                      <span className='txt'>{isActive ? "공개" : "비공개"}</span> <span className={`switch ${isActive ? "active" : ""}`} onClick={handleClick}></span>
                    </ToggleBtn>
                  </Row>
                  {
                      historyRows.map((row,index) => (
                        <Row key={row.id}>
                          <HistoryForm 
                            id={row.id}
                            onDelete={() => removeHistoryRow(row.id)}
                            onMoveUp={() => moveHistoryRowUp(index)}
                            onMoveDown={() => moveHistoryRowDown(index)}
                          />
                        </Row>
                      ))
                    }
                  <LineBtn className='center' onClick={addHistoryRow}>일대기 추가</LineBtn>
                </Section>
                <Section>
                  <FlexSpace>
                    <SecTitle>부고 공개 여부</SecTitle>
                    <ToggleBtn>
                      <span className='txt'>{isOpenActive ? "공개" : "비공개"}</span> <span className={`switch ${isOpenActive ? "active" : ""}`} onClick={openOptClick}></span>
                    </ToggleBtn>
                  </FlexSpace>
                  <SecDesc col_margin="2rem">비공개로 등록하더라도 추후에 공개로 수정하여 등록하고 공유할 수 있습니다.</SecDesc>
                </Section>
                <BtnWrap>
                  <LineBtn onClick={handleCancel}>취소</LineBtn>
                  <Button $primaryBtn>등록완료</Button>
                </BtnWrap>
              </div>
            </FormWrap>
        </Container>
      </form>
      <div className='show-data container' style={{ display: 'none' }}>
        {allValue.map((entry, index) => (
          <div key={index} className="sign-box">
            <h1>Send Successfully</h1>
            <h3>고인: <p>{entry.majorNm}</p></h3>
            <h3>직업: <p>{entry.position}</p></h3>
            <h3>상주 정보:</h3>
            {entry.fmMembers.map((member, idx) => (
              <p key={idx}>
                관계: {member.fmMemberSel}, 이름: {member.fmMemberNm}
              </p>
            ))}
          </div>
        ))}
      </div>
    </>
    
  )
}

export default DtNoticeRegister