import React from 'react'
import { Swiper, SwiperSlide } from 'swiper/react';
import { Autoplay, EffectFade, Navigation, Pagination } from 'swiper/modules';
import { Visual } from '../../pages/Main/main.style';
import 'swiper/css';
import "swiper/css/effect-fade";
import 'swiper/css/navigation';
import imgMain1 from '../../images/main/img_main1.jpg';
import imgMain2 from '../../images/main/img_main2.jpg';


const VisualSlider = () => {
  return (
    <>
      <Visual>
        <div className='visual_inner'>
          <Swiper 
              effect={"fade"} 
              navigation={true} 
              modules={[Autoplay, EffectFade, Navigation]}
              autoplay={{
                  delay: 3000,
                  disableOnInteraction: false,
              }}
          >
              <SwiperSlide><img src={imgMain1} alt=""/></SwiperSlide>
              <SwiperSlide><img src={imgMain2} alt=""/></SwiperSlide>
          </Swiper>
        </div>
      </Visual>
    </>
  )
}

export default VisualSlider