import React from 'react'
import { Container } from '../../common/common.style'
import { BtnGrp, List, MemberContent, MyPageTitle } from './member.style'

const Mypage = () => {
  return (
    <>
      <Container>
        <form action='#'>
          <MemberContent className='form'>
            <h2 className='big_tit'>마이 페이지</h2>
            <MyPageTitle className='border_type'>
              <h3 className='title'>회원설정</h3>
            </MyPageTitle>
            <MyPageTitle>
              <h4 className='form_title'>기본정보</h4>
            </MyPageTitle>
            <List>
              <li>
                <input type="text" id="email" className="" placeholder="이메일"></input>
              </li>
              <li className="auth_row">
                <input type="password" id="pw" className="" placeholder="비밀번호"></input>
                <button>비밀번호 변경</button>
              </li>
              <li>
                <input type="text" id="name" className="" placeholder="이름"></input>
              </li>
              <li>
                <input type="text" id="ph" className="" placeholder="핸드폰 번호"></input>
              </li>
              <li>
                <input type="text" id="address" className="" placeholder="주소"></input>
              </li>
            </List>
            <BtnGrp>
              <button>저장</button>
            </BtnGrp>
          </MemberContent>
        </form>
      </Container>
    </>
  )
}

export default Mypage