import styled from 'styled-components';


export const MemorialUl = styled.ul`
  display:grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 60px;
  a{
    display:block;
    width:100%;
  }
  @media (max-width: 1024px) {
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
  }
  @media (max-width: 768px) {
     grid-template-columns: repeat(2, 1fr);
    gap: 30px;
  }
`


export const Notice = styled.p`
  margin:var(--margin) 0;
  font-size:1.6rem;
  color:#0C1622;
  word-break:keep-all;
`

export const LifeStoryStyle = styled.div`
  padding-top:2rem;
  text-align:center;
`
export const LifeStoryVideo = styled.div`
  width:960px;
  margin:0 auto;
  position:relative;
  video{
    width:100%;
  }
   button{
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    background:url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='135' height='135' fill='none'%3E%3Cpath stroke='%23fff' stroke-linejoin='round' stroke-width='2' d='M134 67.504c0 36.457-29.773 66.012-66.5 66.012-36.727 0-66.5-29.555-66.5-66.012C1 31.046 30.773 1.49 67.5 1.49c36.727 0 66.5 29.555 66.5 66.013Z'/%3E%3Cpath stroke='%23fff' stroke-linejoin='round' stroke-width='2' d='M54.381 93.999V42.805l36.19 25.597-36.19 25.597Z'/%3E%3C/svg%3E") no-repeat 50% 50%;
    cursor:pointer;
  }
  @media (max-width: 1024px) {
    width:80%;
  }
`
export const AlbumList = styled.div`
  margin-top:2rem;
  li a{
    display:block;
    border:1px solid #ccc;
    background-color:#fff;
    width:100%;
    height:100%;
    .img{
      display:block;
      position:relative;
      height:234px;  
      img{
        object-fit:cover;
      }
    }
    .info{
      padding:1.2rem; 2.4rem;
      font-family:var(--f-second);
      text-align:left;
      span{
        color:#9da7bb;
        font-size:1.4rem;
      }
    }
  }

`