import styled from 'styled-components';
import MainTopBg from '../../images/main/main_top_bg.jpg';
import MainArrNext from '../../images/main/main_arr_next.png';
import MainArrPrev from '../../images/main/main_arr_prev.png';
import BgMbContSv from '../../images/main/bg_mb_cont_service.png';
import BgSv1 from '../../images/main/bg_service1.png';
import BgSv2 from '../../images/main/bg_service2.png';
import BgSv3 from '../../images/main/bg_service3.png';
import BgSv4 from '../../images/main/bg_service4.png';
import IcoSchWhite from '../../images/ico_search_white.png';

export const MainWrap= styled.main`
  *{
      transition:.4s;
    }
  .main_conainer{
    margin:80px auto;
  }
 
  @media (max-width: 768px) {
    .main_conainer{
      margin:40px auto;
    }
  }
`
export const TopWrap=styled.div`
  background:url(${MainTopBg}) no-repeat;
  .main_top{
    margin-top:84px;
  }
  @media (max-width: 1024px){
    padding-bottom:60px;
    .main_top{
      margin-top:6rem;
    }
  }
`

export const Visual= styled.div`
  padding-top:180px;
  padding-bottom:80px;
  .visual_inner{
    margin-left:384px;
  }
  .swiper-slide{
    height:634px;
  }
  .swiper-button-next, .swiper-button-prev{
   top:auto;
   bottom:60px;
   left:auto;
   width:42px;
   height:12px;
   background-repeat:no-repeat;
   background:transparent;
   z-index:120;
   text-indent:-9999em;
   &:after{
     display:none;
   }
 }
 .swiper-button-next{
   right:80px;
   background-image:url(${MainArrNext});
 }
 .swiper-button-prev{
   right:150px;
   background-image:url(${MainArrPrev});
 }

@media (max-width: 1024px){
  padding-top:80px;
  padding-bottom:60px;
  .visual_inner{
      margin-left:6%;
    }
  
  .swiper {
    overflow:initial;
  }
  .swiper-slide{
    height:266px;
    img{
      width:100%;
    }
  }
  .swiper-button-next, .swiper-button-prev{
    bottom:-30px;
  }
  .swiper-button-next{
    right:20px;
  }
  .swiper-button-prev{
    right:84px;
  }
}
`
export const TopContent=styled.div`
   position:absolute;
   top:417px;
   z-index:110;
   .text{
      line-height:1.6;
      color:#fff;
      font-size:2rem;
      word-break:keep-all;
   }
   .letter{
      margin:3rem 0;
   }
   @media (max-width:1024px) {
      position:relative;
      top:auto;
      left:auto;
      margin-left:0;
      padding:20px 0;
      width:100%;
      overflow:hidden;
      box-sizing:border-box;
      .letter{
         width:100%;
      }
   }
`
export const SearchForm= styled.div`
  position:relative;
  width:24rem;
  .search_input{
      line-height:6rem;
      padding: 0 8px 0 20px;
      color:#fff;
      font-size:1.8rem;
      border-bottom:none;
      border:2px solid rgba(255,255,255,.5);
      border-radius:50px;
      background:transparent;
      &::placeholder{color:#fff;}
  }
  .ico_sch{
      --icoSize:34px;
      width:var(--icoSize);
      height:var(--icoSize);
      display:inline-block;
      position:absolute;
      top:50%;
      transform:translateY(-50%);
      right:0;
      margin-top:0;
      background:url(${IcoSchWhite}) no-repeat;
      text-indent:-9999em;
  }
`
export const MainSec=styled.section`
  position:relative;
  & + section{
    margin-top:140px;
  }
  .btn_more{
    position:absolute;
    top:0;
    right:20px;
    font-size:1.8rem;
    color:var(--primary-color);
    &:after{
      content:"";
      display:inline-block;
      width:8px;
      height:8px;
      vertical-align:middle;
      margin-top:-4px;
      border-left:1px solid var(--primary-color);
      border-bottom:1px solid var(--primary-color);
      transform:rotate(-135deg);
    }
  }
  @media (max-width: 768px) {
    & + section{
      margin-top:60px;
    }
  }
`
export const ServiceStyle = styled.ul`
   display:grid;
   grid-template-columns: repeat(4, 1fr);
   gap:40px;
   li{
      height:36.4rem;
      text-align:center;
      overflow:hidden;
      background-size:cover;
      border-radius:5px;
      &:hover{
        transform:scale(1.03);
        transition:.4s;
      }
      a{
        display:flex;
        height:100%;
        flex-direction:column;
        justify-content:center;
        gap:20px;
        color:#fff;
      }
      i{
        display:block;
        width:25%;
        height:67px;
        margin:0 auto;
        img{
          height:auto;
        }
      }
      strong{
        display:block;
        font-size:2.6rem;
      }
      p{
        margin: 0 auto;
        word-break: keep-all;
        line-height: 1.6;
        font-size: 1.6rem;
      }
      &.sv1{
        background-image:url(${BgSv1});
      }
      &.sv2{
        background-image:url(${BgSv2});
      }
      &.sv3{
        background-image:url(${BgSv3});
      }
      &.sv4{
        background-image:url(${BgSv4});
      }
   }
   @media (max-width: 1024px) {
      grid-template-columns: repeat(2, 1fr);
      gap:60px;
      li{
         width:300px;
        height:390px;
        margin:0 auto;  
      }
   }
  @media (max-width: 768px) {
    grid-template-columns: repeat(1, 1fr);
      align-items:center;
      gap:40px;
      li{
        a{
          gap:14px;
        }
        i{
          height:44px;
          margin:0 auto 24px;
        }
      }
  }


`
;
