import React from 'react'
import { useNavigate } from 'react-router-dom';
import { MemorialUl} from '../Memorial/memorial.style'
import SearchForm from '../../components/Form/SearchForm';
import ContList from '../../components/List/ContList';
import { Container, FillBtn, Flex, FlexSpace, PageTitle, SecDesc } from '../../common/common.style';
import DtNoticeData from './DtNoticeData';

const DeathNotice = () => {
  const navigate = useNavigate();
  const linkpath = (path) => {
    navigate(path);
  };
  return (
    <>
      <Container>
        <PageTitle>부고</PageTitle>
        <FlexSpace className='mb_column'>
          <SecDesc>메모리얼 캔버스에서 <br/> 43명의 삶을 기억하고 추모합니다.</SecDesc>
          <Flex>
            <SearchForm />
            <FillBtn $primaryBtn onClick={() => linkpath("/deathNoiceReg")}>부고 등록</FillBtn>
          </Flex>
        </FlexSpace>
        <MemorialUl>
          {
              DtNoticeData.map((value,index) =>{
                return <ContList
                  key={value.id}
                  value={value}
                  type="page"
                  link={`/deathNoiceView/${value.id}`}
                />
              })
          }
        </MemorialUl>
      </Container>
    </>
  )
}

export default DeathNotice