import React, { useEffect, useState } from 'react'
import {PageTitle, SecDesc } from '../../common/common.style'
import PopupSendKakao from './PopupSendKakao'
import BtnSend from '../../common/popup/BtnSend'
import PopupSendSms from './PopupSendSms'
import Popup from '../../common/popup/Popup'
import { BtnSendWrap } from '../../common/popup/popup.style'

export const  PopupDeathNote = ({handleClose}) => {
  const [sendKakao, setSendKakao] = useState(false)
  const [sendSms, setSendSms] = useState(false)
  
  const handleSendKakao = () => {
    //setSendKakao(!sendKakao);
    setSendKakao((prev) => {
      console.log('이전 상태:', prev); // 이전 상태 확인
      return !prev; // 새로운 상태로 업데이트
    });
  };
  const handleSendSms = () =>{
    setSendSms(!sendSms)
  }

  
  useEffect(() => {
    console.log('sendKakao 상태 변경:', sendKakao); // 상태가 변경될 때마다 출력
  }, [sendKakao]);

  return (
    <>
       {!sendKakao && (
          <Popup handleClose={handleClose}>
            <PageTitle>부고 전송</PageTitle>
            <SecDesc className='desc'>부고 전달 방법을 선택하세요.</SecDesc>
            <BtnSendWrap>
                <BtnSend 
                  className='kakao' 
                  handleClick={handleSendKakao} 
                  text="카카오톡 전송"
                />
                <BtnSend 
                  className='sms' 
                  handleClick={handleSendSms} 
                  text="문자 전송"
                />
            </BtnSendWrap>
          </Popup>
       )}
      {sendKakao && (
        <div>
          <PopupSendKakao 
           handleClose={() => {
            setSendKakao(false);
            }}
          />
        </div>
      )}

      {sendSms && (
        <div>
          <PopupSendSms
            handleClose={handleSendSms}
          />
        </div>
      )}

     
    </>
  )
}
