import styled from 'styled-components';

export const FaqAcc = styled.div`
  margin-top:40px;
  border-top:2px solid #000;

`
export const AccHeader = styled.button`
  display:block;
   position:relative;
    width:100%;
    padding:2rem 0;
    padding-right:40px;
    border-bottom:1px solid #bbc4d7;
    color:#0c1622;
    font-size:2rem;
    text-align:left;
    word-break:keep-all;
    &:after{
      content:"";
      position:absolute;
      right:14px;
      width:14px;
      height:14px;
      border-bottom:2px solid #0c1622;
      border-right:2px solid #0c1622;
      transform:rotate(45deg);
    }
    &.on:after{
      top:36px;
      transform:rotate(-138deg);
    }
`
export const AccCont = styled.div`
   border-bottom:1px solid #bbc4d7;
  padding:3rem 0;
  font-size:1.6rem;
  line-height:1.6;
   word-break:keep-all;
`