import React from 'react'
import { Grid3ColStick, Label } from './form.style'
import FormSet from './FormSet'
import { FlexSpace } from '../../common/common.style'

function AccountForm({id, onDelete, onMoveUp, onMoveDown}) {
  return (
    <>
      <FlexSpace>
        <Label htmlFor={`acount-${id}`}>계좌</Label>
        <FormSet onDelete={onDelete} onMoveUp={onMoveUp} onMoveDown={onMoveDown}/>
      </FlexSpace>
      <Grid3ColStick>  
        <input type='text' id={`acount-${id}`} placeholder='계좌명' />
        <select id="" className='stick' >
          <option>은행</option>
          <option>옵션1</option>
          <option>옵션2</option>
          <option>옵션3</option>
        </select >
        <input type='text' className='stick' placeholder='계좌번호를 입력하세요'/>
      </Grid3ColStick>
    </>
  )
}

export default AccountForm