import React from 'react'
import { Popupcontent, PopupWrap } from './popup.style'
import { BtnClose } from '../common.style'

const Popup = ({ children, handleClose }) => {
  return (
    <PopupWrap>
      <div className='dim'></div>
      <Popupcontent>
          <div className='inner'>
            <BtnClose 
              position={20} 
              color={'#000'} 
              className='btn_close' 
              onClick={handleClose}>
                <span className='blind'>닫기</span>
            </BtnClose>
            {children}
          </div>
      </Popupcontent> 
    </PopupWrap>
  )
}

export default Popup