import React from 'react'
import { ChkForm, Container, Flex} from '../../common/common.style'
import { List, MemberContent, EmailForm, BtnGrp } from './member.style'
import TopSec from './TopSec'

const MemberJoin = () => {
  return (
    <>
       <Container>
          <form action='#'>
            <MemberContent className='form'>
              <TopSec title="회원가입"/>
              <List>
                <EmailForm>
                  <Flex className="flex">
                    <input type="email" id="email" className="email" placeholder="이메일 주소" />
                    <select>
                      <option value="">naver.com</option>
                      <option value="">gmail.com</option>
                      <option value="">daum.com</option>
                    </select>
                    <input type="text" id="emailDir" className="email_dir" placeholder="직접 입력"  autoComplete="username"/>
                  </Flex>
                </EmailForm>
                <li>
                  <input type="password" id="pw" className="" placeholder="비밀번호" autoComplete="new-password"></input>
                </li>
                <li>
                  <input type="password" id="pwChk" className="" placeholder="비밀번호 확인" autoComplete="new-password"></input>
                </li>
                <li>
                  <input type="text" id="name" className="" placeholder="이름"></input>
                </li>
                <li className="auth_row">
                  <input type="text" id="ph" className="" placeholder="휴대폰 번호"></input>
                  <button>인증번호 발송</button>
                </li>
                <li className="auth_row">
                  <input type="text" id="authNum" className="" placeholder="인증번호 입력"></input>
                  <button>인증코드 확인</button>
                </li>
              </List>
              <ChkForm className="chk_form">
                  <input type="checkbox" name="" className="check" id="id_chk" />
                  <label htmlFor="id_chk">개인정보처리방침</label>
                  <button>보기</button>
              </ChkForm>
              <ChkForm className="chk_form">
                  <input type="checkbox" name="" className="check" id="id_chk2" />
                  <label htmlFor="id_chk2">이용약관</label>
                  <button>보기</button>
              </ChkForm>
              <BtnGrp>
                <button>회원가입</button>
                <button className="kakao">카카오 간편가입</button>
                <button className="naver">네이버 간편가입</button>
              </BtnGrp>
          </MemberContent>
        </form>
       </Container>
    </>
  )
}

export default MemberJoin