import React from 'react'
import { Popupcontent, PopupWrap, SendBoxWrap, SendBox } from '../../common/popup/popup.style'
import { BtnClose, Button, Flex, PageTitle } from '../../common/common.style'
import SendTextBox from '../../components/View/SendTextBox'
import Popup from '../../common/popup/Popup'


const PopupSendSms = ({handleClose}) => {
  return (
    <Popup handleClose={handleClose}>
      <PageTitle>문자 전송</PageTitle>
      <SendBoxWrap>
        <SendBox>
          <h3>문자(MMS) 부고 작성</h3>
          <SendTextBox $border />
        </SendBox>
        <Button $primaryBtn className='center bottom_btn'>보내기</Button>
      </SendBoxWrap>
    </Popup>
  )
}

export default PopupSendSms