import React, { useEffect } from 'react';
import { AccCont, AccHeader, FaqAcc } from './faq.style'
import $ from 'jquery';
import FaqData from './FaqData'
import { Container, PageTitle } from '../../common/common.style';

const Faq = () => {
  useEffect(() => {
    $(".cont").slideUp();
    const handleAccordionClick = function () {
      $(this).next(".cont").stop().slideToggle(300);
      $(this).toggleClass('on').siblings().removeClass('on');
      $(this).next(".cont").siblings(".cont").slideUp(300); // 1개씩 펼치기
    };

    $(".header").on('click', handleAccordionClick);

    // Cleanup function to remove the event handler
    return () => {
      $(".header").off('click', handleAccordionClick);
    };
  }, []);

  return (
      <Container>
        <PageTitle>FAQ</PageTitle>
        <FaqAcc>
          {
             FaqData.map((item,index) =>(
              <div key={index}>
                <AccHeader className='header'>
                    {item.question}
                </AccHeader>
                <AccCont className='cont'>
                  {item.answer}
                </AccCont>
              </div>
           
             ))
          }
          
        </FaqAcc>
    </Container>
  )
}

export default Faq