import styled from 'styled-components';
import IcoKakao from '../../images/ico_login_kakao.png';
import IcoNaver from '../../images/ico_login_naver.png';

export const MemberContent = styled.div`
  width:800px;
  margin:0 auto;

  h2{
    margin-bottom:2rem;
    font-size:1.4rem;
    font-weight:normal;
  }
  h3, h2.big_tit{
    margin-bottom:7rem;
    line-height:var(--txt-lh);
    font-size:2.6rem;
    word-break:keep-all;
    font-weight:normal;
  }
  @media (max-width: 768px) {
    width:100%;
  }
  input{
    border:none;
    border-bottom:1px solid var(--form-border);
  }
  .chk_form{
    margin-bottom:1.4rem;
    button{
      font-size:1.2rem;
      color:#192b57;
      text-decoration:underline;
    }
  }
`
export const List = styled.ul`
  li{
    margin-bottom:2rem;
    font-size:1.6rem;
  }
  .auth_row{
    input{
      width:70%;
    }
    button{
      margin-left:2rem;
      width:12rem;
      height:4rem;
      border:1px solid #000;
      font-size:1.6rem;
    }
  }
  @media (max-width:  768px) {
    .auth_row{
      input{
        width:60%;
      }
    }
  }
`
export const EmailForm = styled.li`
  .flex{
    gap:1rem;
  }
  .email{
    width:80%;
  }
  select{
    padding: .6rem;
    width:40%;
  }
  .email_dir{
    width:60%;
  }
  @media (max-width:  768px) {
    .email{
      width:70%;
    }
    select{
      width:50%;
    } 
  }
`

export const BtnGrp = styled.div`
  margin-top:2rem;
  button{
    display:block;
    width:100%;
    height:5rem;
    text-align:center;
    color:#fff;
    font-size:1.8rem;
    background:var(--primary-color);
    +button{
      margin-top:10px;
    }
    &:before{
      content:"";
      position:absolute;
      top:50%;
      transform:translateY(-50%);
      left:20px;
      background-size:contain;
      background-repeat:no-repeat;
    }
    &.kakao{
      position:relative;
      background:#fee500;
      color:#3b3715;
      &:before{
        width:4.6rem;
        height:4.2rem;
        background-image:url(${IcoKakao});
      }
    }
    &.naver{
      position:relative;
      background:#03c75a;
      color:#3b3715;
      &:before{
        width:3rem;
        height:3rem;
        background-image:url(${IcoNaver});
      }
    }
  }
`
export const MemberGrp = styled.div`
  margin-top:2rem;
  text-align:center;
  button{
    font-size:14px;
    color:#404b59;
    &:first-of-type:before{
      display:none;
    }
    &:before{
      content:"";
      display:inline-block;
      height:10px;
      margin-right:8px;
      border-left:1px solid var(--form-border);
    }
  }
`
export const MyPageTitle = styled.div`
  &.border_type{
    padding-bottom:2rem;
    margin-bottom:6rem;
    border-bottom:2px solid var(--primary-color);
  }
  
  .title{
    margin-bottom:0;
    font-size:1.8rem;
  }
  .form_title{
    font-size:1.6rem;
    margin-bottom:2rem;
    font-weight:normal;
  }
`