import React from 'react'
import { ChkForm, Container, Flex } from '../../common/common.style'
import { BtnGrp, EmailForm, List, MemberContent, MemberGrp } from './member.style'
import { useNavigate } from 'react-router-dom';
import TopSec from './TopSec';

const MemberLogin = () => {
  const navigate = useNavigate();
  const linkpath = (path) => {
    navigate(path);
  };

  return (
    <>
      <Container>
        <form action='#'>
          <MemberContent className='form'>
            <TopSec title="Login"/>
            <List>
                <EmailForm>
                  <Flex className="flex">
                    <input type="email" id="email" className="email" placeholder="이메일 주소" />
                    <select>
                      <option value="">naver.com</option>
                      <option value="">gmail.com</option>
                      <option value="">daum.com</option>
                    </select>
                    <input type="text" id="" className="email_dir" placeholder="직접 입력" autoComplete="username" />
                  </Flex>
                </EmailForm>
              <li>
                <input type="password" id="pw" className="" placeholder="비밀번호" autoComplete="current-password"></input>
              </li>
            </List>
            <ChkForm className="chk_form">
                <input type="checkbox" name="" className="check" id="id_chk" />
                <label htmlFor="id_chk">아이디 저장</label>
            </ChkForm>
            <BtnGrp>
              <button>회원가입</button>
            </BtnGrp>
            <MemberGrp>
              <button onClick={() => linkpath("/memberJoin")}>회원가입</button>
              <button onClick={() => linkpath("/memberFindId")}>아이디 찾기</button>
              <button onClick={() => linkpath("/memberFindPw")}>비밀번호 찾기</button>
            </MemberGrp>
          </MemberContent>
        </form>
      </Container>
    </>
  )
}

export default MemberLogin