import React, { useState } from 'react'
import { BtnSmall, Button, ChkForm, Container, Flex, PageTitle, SecDesc, Section } from '../../common/common.style'
import { BtnFlower, ChkFormBox, FlexgGroup, FlowerList, FlowerStyle, FlowerTexts} from './digitalFlower.style.jsx'
import Req from '../../components/Form/Req';
import ChkFormComp from '../../components/Form/ChkFormComp';
import { useNavigate } from 'react-router-dom';

function DigitalFlowr() {
  const [activeBtns, setActiveBtns] = useState([false, false, false]);

  const handleActive = (index) => {
    setActiveBtns((prev) =>
      prev.map((active, idx) => (idx === index ? !active : active))
    );
  };
  const navigate = useNavigate();
  const linkpath = (path) => {
    navigate(path);
  };
  return (
    <>
      <Container>
        <PageTitle>디지털 화환</PageTitle>
        <div className='inner'>
          <FlowerStyle>
            <Section>
              <h3 className='form_tit'>화환 선택</h3>
              <SecDesc className='sec_desc'>디지털 화환 금액은 상주께 전해집니다.</SecDesc>
              <Flex gap="2" $mbCol>
                <BtnFlower className="type1"  onClick={() => handleActive(0)}>
                  <FlowerTexts>
                    <em>근조화환</em>
                    <span className='amt'>100,000원</span>
                  </FlowerTexts>
                  <BtnSmall className={`btn_txt ${activeBtns[0] && 'primary'}`}>선택</BtnSmall>
                </BtnFlower>
                <BtnFlower className="type2"  onClick={() => handleActive(1)}>
                  <FlowerTexts>
                    <em>국화3송이</em>
                    <span className='amt'>50,000원</span>
                  </FlowerTexts>
                  <BtnSmall className={`btn_txt ${activeBtns[1] && 'primary'}`}>선택</BtnSmall>
                </BtnFlower>
                <BtnFlower className="type3"  onClick={() => handleActive(2)}>
                  <FlowerTexts>
                    <em>국화1송이</em>
                    <span className='amt'>30,000원</span>
                  </FlowerTexts>
                   <BtnSmall className={`btn_txt ${activeBtns[2] && 'primary'}`}>선택</BtnSmall>
                </BtnFlower>
              </Flex>
            </Section>
            <Section>
              <FlexgGroup>
                <label htmlFor="buyer" className='form_tit'>구매자 정보 입력<Req /></label>
                <div className='form col'>
                    <input type ="text" id="buyer" placeholder="성명/회사명/모임명"></input>
                    <input type ="text" id="note" placeholder="남겨주실 문구 입력"></input>
                </div>
              </FlexgGroup>
            </Section>
            <Section>
              <FlexgGroup>
                <h3 className='form_tit'>최종 결제 금액</h3>
                <div className='col'>
                  <FlowerList>
                    <em>상품명</em>
                    <ul>
                      <li><strong>근조화환</strong><span>100,000 원</span></li>
                      <li className='total'><strong>총 결제금액</strong><span>100,000 원</span></li>
                    </ul>
                  </FlowerList>
                </div>
              </FlexgGroup>
            </Section>
            <Section $borderNone>
              <FlexgGroup>
                <label htmlFor="pay_opt1" className='form_tit'>결제 수단<Req /></label>
                <div className='col'>
                  <Flex gap="4">
                    <ChkFormComp text="신용카드" id="pay_opt1"/>
                    <ChkFormComp text="무통장" id="pay_opt2"/>
                  </Flex>
                  <ChkFormBox> 
                    <div className='row'>
                      <ChkFormComp id="chktxt1" text="주문할 상품의 상품명, 상품가격, 주문처리 목적을 확인하였습니다.(전자상거래법 제 8조 2항) 위 주위사항을 화인하였으며 동의합니다." />
                    </div>
                    <div className='row'>
                      <ChkFormComp id="chktxt2" text="디지털 화환 결제시 환불처리 불가함을 확인하였습니다." />
                    </div>
                  </ChkFormBox>
                </div>
              </FlexgGroup>
            </Section>
            <Button className='center' $primaryBtn onClick={() => linkpath("/digitalFlowrOrder")}>결제하기</Button>
          </FlowerStyle>
        </div>
      </Container>
    </>
  )
}

export default DigitalFlowr