import React, { useState } from 'react'
import { Label, ToggleBtn } from './form.style'
import FormSet from './FormSet'
import { FlexSpace } from '../../common/common.style';

const HistoryForm = ({id, onDelete, onMoveUp, onMoveDown}) => {
  const [isActive, setIsActive] = useState(true);
  const handleClick = () => {
    setIsActive(!isActive); 
  };
  return (
    <>
      <FlexSpace>
        <Label htmlFor={`history-${id}`}>일대기</Label>
        <FormSet onDelete={onDelete} onMoveUp={onMoveUp} onMoveDown={onMoveDown}/>
      </FlexSpace>
      <input type='text' id={`history-${id}`} placeholder='제목을 입력하세요(최대 20글자)'/>
      <textarea placeholder='고인의 일대기를 작성하세요'></textarea>
      <ToggleBtn>
        <span className='txt'>{isActive ? "공개" : "비공개"}</span> <span className={`switch ${isActive ? "active" : ""}`} onClick={handleClick}></span>
      </ToggleBtn>
    </>
  )
}

export default HistoryForm