import React, { useState } from 'react'
import { Popupcontent, PopupWrap, SendBoxWrap, SendBox, MajorImg } from '../../common/popup/popup.style'
import { BtnClose, Button, Flex, PageTitle} from '../../common/common.style'
import ChkFormComp from '../../components/Form/ChkFormComp'
import SendTextBox from '../../components/View/SendTextBox'
import SampleImg from '../../images/sample/img_memorial1.jpg'
import Popup from '../../common/popup/Popup'

const PopupSendKakao = ({handleClose}) => {
  const [selectedType, setSelectedType] = useState("image");
  const handleChange = (type) => {
    setSelectedType(type); // 선택된 타입으로 상태 업데이트
  };


  return (
    <Popup handleClose={handleClose}>
        <PageTitle>부고 전송</PageTitle>
        <SendBoxWrap>
          <div className='chk_wrap'>
            <Flex gap="4">
              <ChkFormComp
                id="sendType1"
                text="이미지 부고"
                onChange={() => handleChange("image")}
                checked={selectedType === "image"}
              />
              <ChkFormComp
                id="sendType2"
                text="글 부고"
                onChange={() => handleChange("text")}
                checked={selectedType === "text"}
              />
            </Flex>
          </div>
          {selectedType === "image" && (
            <SendBox className='image_type'>
              <MajorImg>
                <img src={SampleImg} alt="" />
              </MajorImg>
              <SendTextBox />
            </SendBox>
          )}
          {selectedType === "text" && (
            <SendBox>
                <h3>카카오톡-부고 전송 메시지 작성</h3>
                <SendTextBox $border />
                <div className='desc'>※카카오톡 부고 메시지 전송 시 최대 200자 까지 입력할 수 있습니다.</div>
            </SendBox>
          
          )}
          <Button $primaryBtn className='center bottom_btn'>보내기</Button>
      </SendBoxWrap>
    </Popup>
  )
}

export default PopupSendKakao