import styled from 'styled-components';
import BgFile from "../../images/file_upload.png"
import BgVideo from "../../images/video_upload.png"
import BgChk from "../../images/bg_check_on.png"
import IcoDel from "../../images/ico_del.png"
import icoSch from "../../images/ico_inquiry_search.png"

export const FormWrap = styled.div`
  max-width:800px;
    margin:0 auto;
    margin-top:20px;
  }
  input, select{
    width:100%;
  }
  input + textarea{
    margin-top:1rem;
  }
  
  .reg_desc{
    margin:1rem 0;
    font-size:1.4rem;
    color:var(--gray-color);
  }
  @media (max-width: 768px) {
    width:100%;
  }
`
export const ReqStyle = styled.span`
  &:before{
    content:"";
    display:inline-block;
    text-indent:-9999em;
  }
  color:#eca316;
`
export const Desc = styled.span`
  color:#eca316;
  font-size:1.6rem;
`
export const Row = styled.div`
  margin:20px 0 30px;
`

export const Grid2Col = styled.div`
  display:grid;
  grid-template-columns:55% 45%;
  gap:1rem;
`
export const GridStick = styled(Grid2Col)`
  grid-template-columns:40% 60%;
  gap:0;
  .stick{
    border-left:0;
  }
  &.w_half{
    grid-template-columns: repeat(2, 1fr);
  }
`
export const Grid3ColStick =styled(GridStick)`
   grid-template-columns: 1fr 1fr 2fr;
`
export const Valid = styled.span`
  display:block;
  margin-top:1rem;
  color:#ff3232;
  font-size:1.4rem;
`
export const Label = styled.label`
  display:block;
  margin-bottom:10px;
  font-size:1.6rem;
`
export const FileUpload = styled.div`
   input[type='file'] {position: absolute; top: 0; left: 0; width: 0; height: 0; line-height: 0; overflow: hidden; border: 0;}
  .file {
    display: block;
    width:80px;
    height:80px;
    text-indent:-9999em;
    background: url(${props => props.$video ? BgVideo : BgFile}) no-repeat;
    background-size:cover;
    cursor:pointer;
  }
`
export const FileImg = styled.div`
  width:160px;
  height:190px;
  margin-left:2rem;
  border:1px solid var(--form-border);
  overflow:hidden;
  object-fit:cover;
  position:relative; 
  button{
    position:absolute;
    top:4px; 
    right:4px;
    width:30px;
    height:30px;
    &:before,&:after{
      content:"";
      display:block;
      height:2px;
      background-color:#000;
    }
    &:before{
      transform:rotate(45deg);
    }
    &:after{
      margin-top:-2px;
      transform:rotate(-45deg);
    }
  }
`;
export const CheckForm =styled.div`
  .check{
    display:none;
  }
  .check + label::after{
    content:"";
    display:inline-block;
    width:24px;
    height:24px;
    margin-left:1rem;
    border:1px solid var(--form-border);
    vertical-align:middle;
    cursor:pointer;
    box-sizing:border-box;
  }
  .check:checked + label::after{
    border:none;
    background:url(${BgChk}) #0c1622 no-repeat center;
    background-size:53%;
  }
  .check + label{
    font-size:1.6rem;
  }
`
export const FormSetStyle = styled.div`
  display:flex;
  gap:.4rem;
  margin-top:-24px;
  button{
    width:4rem;
    height:4rem;
    border:1px solid var(--form-border);
  }
  button.del{
    background:url(${IcoDel}) no-repeat center;
  }
  button.arr{
    display:flex;
    justify-content:center;
    align-items:center;
    &:before{
      content:"";
      display:inline-block;
      width:1.2rem;
      height:1.2rem;
      border-right:2px solid var(--primary-color);
      border-bottom:2px solid var(--primary-color);
    }
    &.arr.up:before{
      margin-top:6px;
      transform:rotate(-134deg);
    }
    &.arr.down:before{
      margin-top:-6px;
      transform:rotate(45deg);
    }
  }
`
export const FuneralSch = styled.div`
  input{
    width:80%;
  }
  button{
    display:inline-block;
    width:20%;
    height:45px;
    background-color:var(--primary-color);
    color:#fff;
  }
`
export const HsNum = styled.span`
  display:inline-flex;
  justify-content:center;
  align-items:center;
  width:2.4rem;
  height:2.4rem;
  margin-right:.6rem;
  border:1px solid #708CD7;
  border-radius:4px;
  font-size:1.4rem;
  color:#708CD7;
`

export const ToggleBtn = styled.div`
  margin-top:2rem;
  text-align:right;
  margin-right:2px;
  .txt{
    margin-right:.4rem;
    margin-top:2px;
    display:inline-block;
    font-size:14px;
    vertical-align:top;
  }
  .switch{
    display: inline-block; 
    position: relative; 
    width: 4.4rem; 
    height: 2rem; 
    background: linear-gradient(180deg, rgba(201,201,205,1) 0%, rgba(224,225,229,1) 100%); 
    transition: all 0.2s ease; 
    border-radius: 50px;
    cursor:pointer;
    &:after{
      content: ""; 
      display: block; 
      position: absolute; 
      left: -2px; 
      top:-2px; 
      width: 2.4rem; 
      height: 2.4rem; 
      background: #fff; 
      border-radius: 50%; 
      border:1px solid var(--form-border);
      transition: all 0.2s ease;
    }
    &.active{
      background: var(--primary-color);
      &:after{
        left: 2.4rem;
      }
    }
  }
  @media (max-width: 768px) {
    
  }
`
export const SchBox= styled.div`
  position:relative;
  margin-right:40px;
  input{
    width:30rem;
    padding:.8rem 4rem .8rem 1rem;
    font-size:2rem;
    border:0;
    border-bottom:2px solid #0c1622;
  }
  .sch_btn{
    position:absolute;
    top:0;
    right:0;
    width:30px;
    height:30px;
    background:url(${icoSch}) no-repeat;
  }
  @media (max-width: 480px) {
    input{
     width:16rem;
    }
  }
`