import baseImg from '../../images/file_basic.png';
import { useEffect, useRef, useState } from "react";
import { FileImg, FileUpload } from './form.style';
import { Flex } from '../../common/common.style';

export const FileInput = ({onChange}) => {
  const [value, setValue] = useState();
  const [preview, setPreview] = useState();
  const inputRef = useRef();

  const handleChange = (e) => {
    return;
    const nextValue = e.target.files[0];
    setValue(nextValue);
    onChange(nextValue); // 부모로 파일 전달: onChange는 부모의 handleFileImgChange함수
  }

  const handleClearClick = () => {
    const inputNode = inputRef.current;
    if (!inputNode) return;
    inputNode.value = '';
    setValue(null); // 내부 상태도 초기화
    setPreview(null);  // 미리보기 초기화
    onChange(null); // 부모로 null 전달 (파일 초기화 알림)
  }

  useEffect (()=>{
    if(!value) return;
 
    const nextPreview =  URL.createObjectURL(value);
    setPreview(nextPreview);
 
    //side effect정리
    return () => {
     URL.revokeObjectURL(nextPreview);
    }
   }, [value]);
 

  return(
   <>
    <Flex>
      <FileUpload>
        <label htmlFor="fuleUpload" className="file">파일선택
        <input type="file" id="fuleUpload" accept="image/png, image/jpeg"  onChange={handleChange} ref={inputRef} /></label>
      </FileUpload>
      <FileImg>
        <img src={preview || baseImg} alt="이미지 미리보기" />
        {preview &&<button onClick={handleClearClick}><span className="blind">닫기</span></button>}
      </FileImg>
    </Flex>
   </>
  )
}