import styled from 'styled-components';

export const FooterWrap = styled.footer`
  background:#263250;
  --footer-color:#667292;
  .container{
    margin:0 auto;
  }
  .footer_inner{
    position:relative;
    padding:50px 0;
    color:var(--footer-color);
    font-size:1.5rem;
  }
  .footer_logo{
    width:100px;
    height:66px;
  }
  @media (max-width:1024px) {
    .info{
      address,span{
        display:block;
        &:after{
          display:none;
        }
      }
    }
  }
 `
export const ItemList = styled.ul`
  display:flex;
    li{
      &+li:before{
        content:"";
        display:inline-block;
        width:1px;
        height:12px;
        margin:0 10px;
        background-color:#fff;
      }
      &>a{
        display:inline;
        color:#fff;
        font-size:1.6rem;
      }
    }
`

export const Info = styled.div`
   margin:20px 0 40px;
   address{
    font-style:normal;
   }
    address,span{
      display:inline-block;
      line-height:1.4;
      &:after{
        content:"";
        display:inline-block;
        width:1px;
        height:10px;
        margin:0 10px;
        background-color:var(--footer-color);
      }
      &.none_bar:before,&:last-of-type:after{
        display:none;
      }
    }
   @media (max-width:1024px) {
     address,span{
        display:block;
        &:after{
          display:none;
    }
  }
`

export const SnsLinks = styled.div`
   position:absolute;
    top:50px;
    right:0;
    a{
      display:inline-block;
      width:4.8rem;
      height:4.8rem;
      margin:0 6px;
    }
`
