import React, { useEffect, useState } from 'react'
import { BtnSendWrap, QrBox, QrItems, SendMoneyTit } from '../../common/popup/popup.style'
import { FlexSpace, PageTitle} from '../../common/common.style'
import BtnSend from '../../common/popup/BtnSend'
import Popup from '../../common/popup/Popup'
import IcoToss from '../../images/ico_toss_sm.png';
import IcoKakaoPay from '../../images/ico_kakaopay_sm.png';
import ImgQr from '../../images/img_qr.png';

const PopupSendMoney = ({handleClose}) => {
  return (
    <>
      <Popup handleClose={handleClose}>
        <PageTitle>조의금 보내기</PageTitle>
        <SendMoneyTit>
          <FlexSpace className='top'>
              <h2>홍길동 계좌</h2>
              <button className='btn_copy'>복사하기</button>
          </FlexSpace>
          <div className='account'>기업은행(예금주:홍길동) 123123456567</div>
        </SendMoneyTit>
        <BtnSendWrap>
          <BtnSend 
            className='kakao' 
            text="카카오톡 간편 전송"
          />
          <BtnSend 
            className='sms' 
            text="문자 간편 전송"
          />
          <BtnSend 
            className='toss' 
            text="Toss 송금"
          />
          <BtnSend 
            className='kakaopay' 
            text="카카오페이 송금"
          />
          <QrBox>
            <strong>QR코드 송금(PC버전만 가능)</strong>
            <QrItems>
              <div className='item'>
                <span className='ico'><img src={IcoToss} alt='Toss뱅크' /></span>
                <div className='qr'>
                  <img src={ImgQr} alt="" />
                </div>
              </div>
              <div className='item'>
                <span className='ico'><img src={IcoKakaoPay} alt='카카오 페이' /></span>
                <div className='qr'>
                  <img src={ImgQr} alt="" />
                </div>
              </div>
            </QrItems>
          </QrBox>
        </BtnSendWrap>
      </Popup>
    </>
  )
}

export default PopupSendMoney