import React from 'react'
import { GridStick, Label } from './form.style'
import FormSet from './FormSet'
import { CondolenceFormStyle } from '../List/contList.style'
import { FlexSpace } from '../../common/common.style'

function CondolenceForm({id, onDelete, onMoveUp, onMoveDown}) {
  return (
    <>
      <CondolenceFormStyle>
        <FlexSpace>
          <Label htmlFor={`condolenceNote-${id}`}>조문보</Label>
          <FormSet onDelete={onDelete} onMoveUp={onMoveUp} onMoveDown={onMoveDown}/> 
        </FlexSpace>
        <GridStick className='w_half'>  
          <select id={`condolenceNote-${id}`}>
            <option>고인관계</option>
            <option>부</option>
            <option>모</option>
            <option>아들</option>
            <option>딸</option>
          </select>
          <input type='text' id="" className='stick'/>
        </GridStick>
        <textarea id="" placeholder="" />
      </CondolenceFormStyle>
    </>
  )
}

export default CondolenceForm