import styled from 'styled-components';
import icoArrow from '../images/ico_moreBtn.png'
import BgChk from "../images/bg_check_on.png"

export const Container = styled.div`
  max-width: var(--cont-width);
  margin: 200px auto 8rem;
  padding-left:2rem;
  padding-right:2rem;
  transition:.4s;
  box-sizing:border-box;
  &.wide{
    max-width: var(--cont-width-wide);
  }
  .inner{
    margin:4rem 0;
  }
  @media (max-width: 1024px) {
    margin-top:12rem;
    width: 100% !important;
  
  }
  
`
export const Flex =styled.div.withConfig({
  shouldForwardProp: (prop) => !["gap", "alOpt"].includes(prop),
})`
  display:flex;
  align-items: ${(props) => (props.alOpt ? props.alOpt : 'center')};
  gap: ${(props) => (props.gap ? props.gap : '1')}rem;
  @media (max-width: 1024px) {
    flex-direction: ${(props) => props.$mbCol && 'column'};
     align-items:flex-start;
  }
`

export const FlexSpace = styled(Flex)`
  justify-content:space-between;
  &.mb_column{
    @media (max-width: 1024px) {
      margin-bottom:5rem;
      flex-direction:column;
      align-items:flex-start;
    }
  }
`

export const GridUl = styled.ul.withConfig({
  shouldForwardProp: (prop) => !["columns", "gap"].includes(prop),
})`
  display:grid;
  grid-template-columns: repeat(${(props) => props.columns}, 1fr);
  gap: ${(props) => props.gap || '2rem'};
  @media (max-width: 768px) {
     grid-template-columns: repeat(2, 1fr);
  }
`

export const PcBlock = styled.span`
  display:block;
  @media (max-width: 768px) {
      display:inline;
   }

`
export const PageTitle = styled.h2`
  font-size:3rem;
  font-family: 'Arita-buri';
`
export const SecTitle = styled.h3`
  font-size:2.6rem;
   font-family: 'Arita-buri';
`
export const Date = styled.span`
   display:block;
  font-size:16px;
  color:var(--gray-color);
  font-family: 'Arita-buri';
`

export const MoreBtn = styled.button`
  position:relative;
  display:block;
  margin:4rem auto;
  width:10rem;
  height:3.3rem;
  padding:0 2rem;
  color:#1f1f1f;
  border:1px solid #ccc;
  border-radius:50px;
  text-align:left;
  &:after{
    content:"";
    display:inline-block;
    width:1.3rem;
    height:.8rem;
    position:absolute;
    top:50%;
    right:20px;
    margin-top:1px;
    background:url(${icoArrow}) no-repeat;
    background-size:cover;
    transform:translateY(-50%);
  }
  @media (max-width: 768px) {
    &:after{
      right:10px;
    }
  }
`
export const BtnWrap = styled.div`
  text-align:center;
  button+button{
    margin-left:1rem;
  }
`
export const Button = styled.button`
  display:inline-block;  
  height: 5rem;
  line-height: 5rem;
  color: #fff;
  background-color:  ${props => props.$primaryBtn ?  'var(--primary-color)' : '#eca316'};
  border-radius: 50px;
  font-size: 2rem;
  padding: 0 var(--margin);
  box-sizing: border-box;
  transition:.4s;
  font-family: 'Arita-buri';
  
  &:hover{
     background-color:  ${props => props.$primaryBtn ?  'rgba(71,85,121)' : 'rgba(236,163,22,.8)'};
  }
  &.center{
    margin:0 auto;
    display:block;
  }
`
export const BtnSmall = styled(Button).attrs({ as: "div" })`
  height: 4rem;
  line-height: 4rem;
  font-size: 1.6rem;
  padding: 0 2rem;
`


export const FillBtn = styled(Button)`
  min-width:18rem;
  @media (max-width: 480px) {
     min-width:14rem;
     font-size:1.6rem;
  }
`
export const LineBtn = styled(Button)`
  background:#fff;
  border:1px solid var(--primary-color);
  color:var(--primary-color);
  &:hover{
    background-color:transparent;
  }
    
`
export const Section = styled.section`
  padding-bottom:4rem;
  margin-bottom:var(--margin);
  border-bottom:${props => props.$borderNone ?  'none' : '1px solid var(--form-border)'}; 
  .sec_title{
    margin-bottom:2rem;
    .req{
      vertical-align:super;
    }
  }
  .sub_title{
    margin-top:2rem;
    font-size:2.2rem;
  }
  &.tx_center, .tx_center{
    text-align:center;
  }
`
export const SecDesc= styled.div.withConfig({
  shouldForwardProp: (prop) => prop !== "col_margin", // col_margin을 DOM에 전달하지 않음
})`
  margin-top:${(props) => (props.col_margin ? props.col_margin : 'var(--margin)')}; 
  margin-bottom:${(props) => (props.col_margin ? props.col_margin : 'var(--margin)')}; 
  line-height: var(--txt-lh);
  font-size:1.8rem;
  color:var(--gray-color);
  word-break:keep-all;
  @media (max-width: 768px) {
     margin:2rem 0;
  }
`
export const BtnClose = styled.button.withConfig({
    shouldForwardProp: (prop) => !["position", "color"].includes(prop), 
  })`
  position:absolute;
  top:${(props) => (props.position ? props.position : 0)}px;
  right:${(props) => (props.position ? props.position : 0)}px;
  z-index:10;
  &:before,&:after{
    content:"";
    width:2px;
    height:18px;
    display:block;
    background-color:${(props) => (props.color ? props.color : '#fff')};
  }
  &:before{
    transform: rotate(45deg);
  }
  &:after{
    margin-top:-18px;
    transform:rotate(-45deg);
  }
`
export const ChkForm = styled.div`
  .check{
    display:none;
  }
  label{
    display:flex;
    font-size:1.6rem;
    word-break:keep-all;
  }
  label:before{
    content:"";
    display:inline-block;  
    border:1px solid var(--form-border);
    width:2.4rem;
    height:2.4rem;
    margin-right:.8rem;
    vertical-align:middle;
    cursor:pointer;
    flex-shrink:0;
    box-sizing:border-box;
  } 
  .check:checked + label:before{
    border:none;
    background:url(${BgChk}) #0c1622 no-repeat center;
    background-size:54%;
  }
`
;