import React from 'react'
import { Container } from '../../common/common.style'
import { BtnGrp, List, MemberContent } from './member.style'
import TopSec from './TopSec'

const MemberFindId = () => {
  return (
    <>
      <Container>
        <form action='#'>
          <MemberContent className='form'>
            <TopSec title="Login"/>
            <List>
              <li>
                <input type="password" id="pw" className="" placeholder="비밀번호"></input>
              </li>
              <li className="auth_row">
                <input type="text" id="" className="" placeholder="휴대폰 번호"></input>
                <button>인증번호 발송</button>
              </li>
              <li className="auth_row">
                <input type="text" id="" className="" placeholder="인증번호 입력"></input>
                <button>인증코드 확인</button>
              </li>
            </List>
            <BtnGrp>
              <button>아이디 찾기</button>
            </BtnGrp>
          </MemberContent>
        </form>
      </Container>
    </>
  )
}

export default MemberFindId