import { SchBox } from './form.style'

const SearchForm = () => {
  return (
    <SchBox>
      <form action="#">
          <fieldset>
              <legend>고인명검색</legend>
              <input type="text" className="sch_input" placeholder="고인명검색" title="고인명을 검색하세요" />
              <button className="sch_btn" type="submit"><span className="blind">검색</span></button>
          </fieldset>
      </form>
    </SchBox>
  )
}

export default SearchForm