import styled from 'styled-components';
import bgFlower1 from '../../images/bg_flower1.jpg'
import bgFlower2 from '../../images/bg_flower2.jpg'
import bgFlower3 from '../../images/bg_flower3.jpg'
import bgFlowerThum from '../../images/bg_flower_thum.png'

export const FlowerStyle = styled.div`
  .form_tit{
    font-size:1.8rem;
    font-weight:normal;
  }
  .sec_desc{
    margin:1rem 0;
    font-size:1.6rem;
  }
`
export const BtnFlower = styled.button`
  display:block;
  width:100%;
  height:15rem;
  background-repeat:no-repeat;
  text-align:left;
  padding-left:14rem;
  position:relative;
  &.type1{
    background-image:url(${bgFlower1})
  }
  &.type2{
    background-image:url(${bgFlower2})
  }
  &.type3{
    background-image:url(${bgFlower3})
  }
  .btn_txt{
    position:absolute;
    top:50%;
    right:2rem;
    transform:translateY(-50%);
    background-color:#BAC1D3;
    &.primary{
      background-color:var(--primary-color)
    }
    &:hover{
      background-color:none;
    }
  }
  @media (max-width: 1024px) {
    width:42rem;
    padding-left:18rem;
  }
`
export const FlowerTexts = styled.div`
   em{
    display:block;
    margin-bottom:.4rem;
    font-size:1.6rem;
  }
  .amt{
    font-size:2rem;
    font-family:var(--f-second);
  }
`

export const FlexgGroup = styled.div`
   display:flex;
  .form_tit{
    width:20rem;
    margin-top:1rem;
  }
  .col{
    width:100%;
  }
`
export const FlowerList = styled.div`
  em{
    display:block;
    margin-bottom:1.4rem;
    color:var(--gray-color);
    font-size:1.4rem;
  }
  li{
    display:table;
    strong,span{
      display:table-cell;
      width:180px;
      font-size:1.6rem;
    }
    &.total{
      margin-top:1.6rem;
      strong,span{
        font-size:1.8rem;
      }
      span{
        color:var(--red-color);
      }
    }
  }  
`

export const ChkFormBox = styled.div`
  margin-top:2rem;
  padding:2rem;
  border:1px solid var(--form-border);
  .row+.row{
    margin-top:1rem;
  }
`

export const BuyInfoBox = styled(ChkFormBox)` 
  padding:2rem 4rem;
  margin-bottom:2rem; 
  display:flex;
  align-items:center;
  gap:10rem;
  width:100%;
  box-sizing:border-box;
  .left{
    flex:1;
    display:flex;
    align-items:center;
    gap:2rem;
    &:before{
      content:"";
      width:13rem;
      height:13rem;
      display:block;
      background:url(${bgFlowerThum}) no-repeat;
    }
  }
  .right{
    flex:3;
    width:100%;
  }
  @media (max-width: 1024px) {
    flex-direction:column;
    .left{  
      &:before{
        background-size:cover;
      }
    }
  }
  
`
export const BuyInfoList=styled.ul`
  display:flex;
  flex-wrap:wrap;
  li{
    display:table;
    margin-bottom:2rem;
    width:50%;
    em,span{
      display:table-cell;
      font-size:1.4rem;
    }
    em{
      width:100px;
      color:var(--gray-color);
    }
  }
 

`