import React from 'react'
import { FormSetStyle, GridStick, Label } from './form.style'
import FormSet from './FormSet';
import { FlexSpace } from '../../common/common.style';

const FmMemberForm = ({id, fmMemberSel, fmMemberNm, onUpdate, onDelete, onMoveUp, onMoveDown }) => {
  const handleSelectChange = (e) => {
    onUpdate(id, "fmMemberSel", e.target.value);
  };

  // 이름 변경
  const handleNameChange = (e) => {
    onUpdate(id, "fmMemberNm", e.target.value);
  };

  return (
    <>
      <FlexSpace>
        <Label htmlFor={`fmMember-${id}`}>상주</Label>
          <FormSet onDelete={onDelete} onMoveUp={onMoveUp} onMoveDown={onMoveDown}/>
      </FlexSpace>
      <GridStick>  
        <select id={`fmMember-${id}`}
          name={`fmMemberSel-${id}`}
          value={fmMemberSel}
          onChange={handleSelectChange}
        >
          <option>고인관계</option>
          <option>부</option>
          <option>모</option>
          <option>아들</option>
          <option>딸</option>
        </select>
        <input type='text' className='stick' placeholder="상주명을 입력하세요" 
          name={`fmMemberNm-${id}`}
          value={fmMemberNm}
          onChange={handleNameChange}
        />
      </GridStick>
    
    </>
   
  )
}

export default FmMemberForm