import styled from 'styled-components';
import BgtitleL from '../../images/bg_address_l.png'
import BgtitleR from '../../images/bg_address_r.png'
import BtnPlay from  '../../images/btn_play.png'

export const ViewWrap = styled.div`
 margin-top:40px;
`
export const MajorImgStyle = styled.div`
  width:25rem;
  height:38rem;
  margin:0 2rem;
  position:relative;
`
export const MajorWrap = styled.div`
  display:flex;
  gap:8rem;
  .col{
    width:80%;
  }
  @media (max-width: 1024px){
    flex-direction:column;
    align-items:center;
    .col{
      width:100%;
    }
  }
`
export const MajorInfo = styled.div`
   .edit_btn{
      padding:0;
      border-bottom:1px solid #9DA7BB;
      font-size:1.2rem;
      color:#9DA7BB;
    }
  .date{
    margin-top:1rem;
    font-size:1.8rem;
  }
  &.memorial{
    width:60%;
    line-height: var(--txt-lh);
    text-align:center;
    .flex{
      justify-content:center;
    }
  }
`
export const Detail = styled.ul`
  margin:var(--margin) 0;
  li{
    line-height:1.8;
    display:table;
    .col{
      display:table-cell;
      width:auto;
      &.title{
       width:10rem;
       font-size:1.8rem;
      }
      &.text{
        font-size:1.6rem;
        color:var(--gray-lg-dk-color);
        em{
          color:#000;
          font-weight:bold;
          margin-right:.4rem;
        }
      }
    }
  }
  
`
export const Address = styled.div`
  .text_area{
    text-align:center;
    .title{
      display:flex;
      justify-content:center;
      align-items:center;
      gap:2rem;
      &:before,&:after{
        content:"";
        background-repeat:no-repeat;
        background-size:cover;
        width:7.8rem;
        height:1.9rem;
      }
      &:before{
       background-image:url(${BgtitleL});
      }
      &:after{
       background-image:url(${BgtitleR});
      }
    }
  }
  p{
    font-size:1.4rem;
    line-height:1.2;
    word-break:keep-all;
    color:var(--gray-lg-dk-color);
  }

`
export const TabHeaderLi= styled.li`
  padding-bottom:1.6rem;
  border-bottom:${props => props.$actColor ? '2px solid var(--primary-color)' : '2px solid var(--form-border)'};
  button{
    display:block;
    width:100%;
    height:100%;
    color:${props => props.$actColor ? 'var(--primary-color)' : 'var(--gray-color)'};
    font-size:1.8rem;
  }
`
export const TabContStyle = styled.div`
  padding:2rem 0 4rem;
  
`
export const PreHistory = styled.div`
  padding:8rem;
  display:flex;
  align-items:center;
  gap:90px;
  font-size:1.6rem;
  figure{
      flex:1;
      img{
        object-fit:cover;
      }
  }
  figcaption{
    flex:2;
    .title{
      margin-bottom:2rem;
      line-height:1.4;
      color:#000;
      font-size:2.8rem;
      word-break:keep-all;
    }
    .text_area{
      margin-top:2rem;
      padding-top:2rem;
      border-top:1px solid #ccc;
      p{
        line-height:1.4;
      }
    }
  }
  .btn_more{
    width:130px;
    height:5rem;
    margin-top:2rem;
    font-size:1.6rem;
    border-radius:50px;
    border:1px solid #192b57;
  }
  @media (max-width: 1024px) {
    flex-direction:column;
    text-align:center;
    gap:6rem;
  }
`

export const RegisterForm=styled.div`
  margin-bottom:40px;
  font-size:1.6rem;
  .row{
    margin-bottom:10px;
  }
  .form{
    display:block;
    width:100%;
    margin-bottom:10px;
    padding:1rem 2rem;
    border-radius:5px;
    border:1px solid #ced5e4;
    box-sizing:border-box;
    font-size:1.6rem;
    background:#fff;
  }
  .form:::placeholder {
    color: #8893aa;
  }
  .select{
    height:4.6rem; 
  }
  textarea{
    height:260px;
  }
  .input_row{
    display:flex;
    input[type='text']{
      width:50%;
      &:first-of-type{
        border-top-right-radius:0;
        border-bottom-right-radius:0;
      }
      &:last-of-type{
        border-top-left-radius:0;
        border-bottom-left-radius:0;
        border-left:none;
      }
    }
  }
  .register_btn{
    padding:30px 0;
    text-align:center;
    button{
      padding:0 20px;
      font-size:2rem;
      line-height:5rem;
      border-radius:50px;
      color:#fff;
    }
  }
  
`
export const ImgList = styled.ul`
  display:flex;
  gap:6px;
  flex-wrap:wrap;
  li{
    position:relative;
    width:97px;
    height:68px;
    border-bottom:none;
    &.movie{
      &:after{
        content:"";
        position:absolute;
        top:50%;
        left:50%;
        transform:translate(-50%,-50%);
        width:36px;
        height:36px;
        background:url(${BtnPlay});
        background-size:cover;
      }
    }
  }
`
export const FileBox = styled.div`
  --pd-col:2rem;
  --bd-rd:.5rem;
  label{
    display:block;
    width:100%;
    height:46px;
    padding:1.2rem 2rem;
    color:var(--primary-color);
    background-color:#edf1f8;
    border:1px solid #bbc4d7;
    border-radius:var(--bd-rd);
    box-sizing:border-box;
    cursor:pointer;
  }
  .fileNone{
    position:absolute;
    top:0;
    left:0;
    width:0;
    height:0;
    line-height:0;
    overflow:hidden;
    border:0;
  }
  .file_list{
    padding:2rem var(--pd-col);
    background:#f5f5f5;
    border-radius:var(--bd-rd);
    li{
      &:before{
        content:"";
        position:absolute;
        top:0;
        left:0;
        width:100%;
        height:100%;
        background-color:rgba(0, 0, 0, 0.3);
      }
    }
    .btn_close{
      &:before,&:after{
        width:1px;
        height:12px;
      }
      &:after{
        margin-top:-12px;
      }
    }
  }
 
`
export const CommendForm=styled.div`
  .commend_box{
      border-top:2px solid var(--primary-color);
  }
  li{
    display:flex;
    gap:10px;
    position:relative;
    padding:14px 0;
    border-bottom:1px solid #dfe4ee;
    font-size:1.8rem;
    letter-spacing:-0.04em;
    strong{
      position:relative;
      min-width:7%;
      color: var(--gray-lg-dk-color);
      font-size:1.6rem;
      &:after{
        content:"";
        width:1px;
        height:16px;
        position:absolute;
        top:0;
        right:-6px;
        background-color:var(--primary-color);
      }
    }
    .text_area{
      padding:0 20px;
      padding-right:40px;
      li{
        border-bottom:none;
      }
      p{
        font-size:1.4rem;
      }
    }
    .btn_close{ 
      right:0;
    }
  }
  @media (max-width: 768px) {
    li{
      flex-direction:column;
      strong{
        &:after{
          display:none;
        }
      }
      .text_area{
        padding:0;
      }
    }
  }
`