import React, { useEffect, useState } from 'react'
import { Button, Container, Date, Flex, PageTitle, SecDesc, Section, SecTitle } from '../../common/common.style'
import { ViewWrap,MajorWrap,MajorInfo, Detail, Address, TabContStyle } from '../../components/View/view.style'
import MajorImg from '../../components/View/MajorImg'
import {SendStyle } from './deathNotice.style'
import { useNavigate, useParams } from 'react-router-dom'
import DtNoticeData from './DtNoticeData'
import FlowrSlides from './FlowrSlides'
import TabHeader from '../../components/View/TabHeader'
import MemorialRegister from '../../components/View/MemorialRegister'
import FuneralInfo from './FuneralInfo'
import CondolenceNote from '../../components/View/CondolenceNote'
import { PopupDeathNote } from './PopupDeathNote'
import PopupSendMoney from './PopupSendMoney'

const DeathNoticeView = ({}) => {
  const {id} = useParams()
  const [item, setItem] = useState(null)
  const [activeTab, setActiveTab] = useState(1);
  const [sendOptions, setSendOptions] = useState(false)
  const [sendMoney, setSendMoney] = useState(false)
  const navigate = useNavigate();
  const linkpath = (path) => {
    navigate(path);
  };

  const TabItems = [
    {id:1, value: "장례정보"},
    {id:2, value: "조문보"},
    {id:3, value: "추모의 글"},
  ]
  const columnCount = TabItems.length; 
  
  const handleTabClick = (tabId) => {
    setActiveTab(tabId);
    //console.log(`Active Tab: ${tabId}`);
  };
  //부고 팝업 
  const handleSendOptions = () =>{
    setSendOptions(!sendOptions)
  }
  const handleSendMoney = () =>{
    setSendMoney(!sendMoney)
  }

  useEffect(() => {
    let item = DtNoticeData.find((item) => item.id === parseInt(id))
    if(item){
      setItem(item)
    }
  }, [id]);

  useEffect(() => {
    if (sendOptions || sendMoney) {
      document.body.classList.add("scroll_hidden");
    } else {
      document.body.classList.remove("scroll_hidden");
    }

    // 컴포넌트가 언마운트될 때도 클래스 제거
    return () => {
      document.body.classList.remove("scroll_hidden");
    };
  }, [sendOptions, sendMoney]);
  
  
  if (!item) {
    return <p>Loading...</p>; // item이 null인 경우 로딩 상태를 표시
  }
  return (
    <>
      <Container className='wide'>
        <PageTitle>부고</PageTitle>
        <ViewWrap>
          <Section>
            <MajorWrap>
              <MajorImg majorImg={item.img}/>
              <MajorInfo className='col'>
                <Flex>
                  <SecTitle>{item.name}</SecTitle>
                  <button className='edit_btn'>수정</button>
                </Flex>
                <Date className='date'>1946.01.03~2022.08.20</Date>
                <Detail>
                  <li><strong className='col title'>상주</strong>
                    <span className='col text'><em>홍철수</em><em>홍가성</em><em>홍지성</em></span>
                  </li>
                  <li>
                    <strong className='col title'>빈소</strong>
                    <span className='col text'>메모리얼장례식장</span>
                  </li>
                  <li>
                    <strong className='col title'>장지</strong>
                    <span className='col text'>분당 하늘공원</span>
                  </li>
                  <li>
                    <strong className='col title'>알림글</strong>
                    <span className='col text'>고인이 가시는 마지막 길에 평안과 명복을 빌어주시면 감사드리겠습니다.</span>
                  </li>
                </Detail>
                <SendStyle>
                  <Button $primaryBtn className='send_first_btn' onClick={handleSendOptions}>부고 전송</Button>
                  <div className='send_others_btn'>
                    <button className='send_btn' onClick={handleSendMoney}>조의금 보내기</button>
                    <button className='send_btn flower' onClick={() => linkpath("/digitalFlowr")}>디지털 화환</button>
                  </div>
                </SendStyle>
              </MajorInfo>
            </MajorWrap>
          </Section>
          <Section className='tx_center'>
            <SecTitle>화환 해주신 분</SecTitle>
            <SecDesc>디지털 화환으로 애도의 뜻을 전합니다.</SecDesc>
            <FlowrSlides />
          </Section>
          <Section>
            <Address>
              <div className='text_area'>
                <SecTitle className='ft_ariburi title'>추도사</SecTitle>
                <SecDesc>고인의 아름다운 생애</SecDesc>
              </div>
              <p>
              선마을이 개원된 지 10년을 넘어간다. 명실공히 예방 캠프로서 완벽한 프로그램으로 정평이 났다. 뭐니해도 젊은 경영인의 미래를 예측하는 안목이 바탕이 되었다. 그리고 지금은 경영일선에서 물러났지만, 총회장의 보이지 않는 지원이 절대적이었다.
              <br/><br/>
              낡은 골프채가 맺어준 인연으로 석천에 대한 나의 존경심은 절대적이다. 가끔 찾아뵐 적마다 언제나 반갑게 맞아주시고 선마을 안부도 함께 물어봐 주신다.
              지난번엔 석천 연구재단 기금으로 많은 산림 치유 교수들이 공동으로 선마을 프로그램에 대한 연구평가를 시행했다. 세계적인 예방 캠프 프로그램이라는 평가를 받았다. 이로써 한국 의료사에 하나의 큰 획이 그어졌다. 그리고 내 개인적으로는 의사로서의 내 생애 가장 보람된 일이라고 자평하고 있다. 나름대로 긍지를 가지고 있다. 내게 있어 선마을은 떼려야 뗄 수 없는 내 아이덴티티의
              일부가 되었다.<br/>
              낡은 골프채가 맺어준 인연으로 석천에 대한 나의 존경심은 절대적이다. 가끔 찾아뵐 적마다 언제나 반갑게 맞아주시고 선마을 안부도 함께 물어봐 주신다.
              지난번엔 석천 연구재단 기금으로 많은 산림 치유 교수들이 공동으로 선마을 프로그램에 대한 연구평가를 시행했다. 세계적인 예방 캠프 프로그램이라는 평가를 받았다. 이로써 한국 의료사에 하나의 큰 획이 그어졌다. 그리고 내 개인적으로는 의사로서의 내 생애 가장 보람된 일이라고 자평하고 있다. 나름대로 긍지를 가지고 있다. 내게 있어 선마을은 떼려야 뗄 수 없는 내 아이덴티티의
              일부가 되었다.<br/>
              낡은 골프채가 맺어준 인연으로 석천에 대한 나의 존경심은 절대적이다. 가끔 찾아뵐 적마다 언제나 반갑게 맞아주시고 선마을 안부도 함께 물어봐 주신다.
              지난번엔 석천 연구재단 기금으로 많은 산림 치유 교수들이 공동으로 선마을 프로그램에 대한 연구평가를 시행했다. 세계적인 예방 캠프 프로그램이라는 평가를 받았다. 이로써 한국 의료사에 하나의 큰 획이 그어졌다. 그리고 내 개인적으로는 의사로서의 내 생애 가장 보람된 일이라고 자평하고 있다. 나름대로 긍지를 가지고 있다. 내게 있어 선마을은 떼려야 뗄 수 없는 내 아이덴티티의
              일부가 되었다.<br/>
              낡은 골프채가 맺어준 인연으로 석천에 대한 나의 존경심은 절대적이다. 가끔 찾아뵐 적마다 언제나 반갑게 맞아주시고 선마을 안부도 함께 물어봐 주신다.
              지난번엔 석천 연구재단 기금으로 많은 산림 치유 교수들이 공동으로 선마을 프로그램에 대한 연구평가를 시행했다. 세계적인 예방 캠프 프로그램이라는 평가를 받았다. 이로써 한국 의료사에 하나의 큰 획이 그어졌다. 그리고 내 개인적으로는 의사로서의 내 생애 가장 보람된 일이라고 자평하고 있다. 나름대로 긍지를 가지고 있다. 내게 있어 선마을은 떼려야 뗄 수 없는 내 아이덴티티의
              일부가 되었다.<br/>
              <br/>
              개인적으로도 어려운 일이 있을 적엔 석천의 낡은 골프채를 생각한다. 내 인생 여정의 훌륭한 반려자가 된 셈이다. 의사로서의 후반 인생을 석천의 그늘로 인해 빛나게 해준 점, 감사와 존경을 드리며.
              </p>
            </Address>
          </Section>
          <Section>
            <TabHeader 
              TabItems={TabItems} 
              columnCount={columnCount}
              activeTab={activeTab}
              handleTabClick={handleTabClick} 
              />
            <TabContStyle>
              {activeTab === 1 && 
                <FuneralInfo />
              }
              {activeTab === 2 && 
                <CondolenceNote />
              }
              {activeTab === 3 && 
                <MemorialRegister />
              }
            </TabContStyle>
          </Section>
      
        </ViewWrap>
      </Container>
      {
        sendOptions && (
         <>
          <PopupDeathNote handleClose={handleSendOptions}/>
         </>
      )}
      {
        sendMoney && (
         <>
          <PopupSendMoney handleClose={handleSendMoney}/>
         </>
      )}
    </>
  )
}

export default DeathNoticeView