import React from 'react'
import { FormSetStyle } from './form.style';

const FormSet = ({onDelete, onMoveUp, onMoveDown }) => {
  return (
    <div>
      <FormSetStyle>
          <button className="del" onClick={onDelete}><span className="blind">삭제</span></button>
          <button className="arr up" onClick={(e) => {
            e.preventDefault();
            onMoveUp();}}><span className="blind">위로이동</span></button>
          <button className="arr down" onClick={(e) => {
            e.preventDefault();
            onMoveDown();
          }}><span className="blind">아래로이동</span>
          </button>
      </FormSetStyle>
    </div>
  )
}

export default FormSet