import styled from 'styled-components';

import badge from "../../images/badge.png"

export const ImgArea = styled.div`
  height:380px;
  position:relative;
  @media (max-width: 1024px) {
    height:100%;
  }
  .badge{
    position:absolute;
    top:-18px;
    left:-18px;
    width:8.8rem;
    height:8.8rem;
    background:url(${badge}) no-repeat;
    background-size:cover;
    text-indent:-9999em;
    z-index:12;
  }
`
export const BgFrame = styled.span`
  display:block;
  height:100%;
  position:relative;
  z-index:10;
  margin-left:-4px;
  img{
    object-fit:cover;
  }
`

  export const Img = styled.span`
  display:block;
  width:90%;
  height:280px;
  position:absolute;
  top:10px;
  left:50%;
  transform:translateX(-50%);
  z-index:1;
  img{
    object-fit:cover;
  }
  @media (max-width: 1024px) {
    height:74%;
  }
  
`

export const MemorialInfo = styled.div`
  margin-top:2rem;
  text-align:center;
  strong{
    display:block;
    margin-bottom:1.6rem;
    color:var(--primary-color);
    font-size:2rem;
    font-family: 'Arita-buri';
  }
  button{
    margin-top:2rem;
  }
`
export const FuneralInfo = styled.ul`
  margin-top:2rem;
  padding-top:1.6rem;
  font-size:1.6rem;
  border-top:1px solid #ccc;
  li{
    margin-bottom:1rem;
    text-align:left;
    em{
      display:inline-block;
      width:20%;
      font-weight:bold;
    }
  }
`
export const CondolenceFormStyle = styled.div`
  textarea{
    border-top:none;
  }
`