import React from 'react'
import {ImgArea, BgFrame, Img, MemorialInfo, FuneralInfo } from './contList.style'
import {Button, Date} from '../../common/common.style'
import bgFrame from "../../images/frame.png"
import { Link } from 'react-router-dom'

const ContList = ({ value: { img, name, date, conffin, mortuary }, type, link }) => {
  return (
    <>
      <li>            
        <Link to={link}>
          <ImgArea>
            {conffin && type === "main" && <i className='badge'>부고뱃지</i>}
            <BgFrame>
              <img src={bgFrame} alt="" />
            </BgFrame>
            <Img>
              <img src={img} alt="" />
            </Img>
          </ImgArea>
          <MemorialInfo>
            <strong>{name}</strong>
            <Date>{date}</Date>
            {conffin && type === "page" && 
              <FuneralInfo>
                <li><em>발인</em>{conffin}</li>
                <li><em>빈소</em>{mortuary}</li>
                
              </FuneralInfo>
            }
            {
              conffin ? 
              <Button>조문하기</Button> 
              :<Button $primaryBtn>추모</Button> 
            }
          </MemorialInfo>
        </Link>
      </li>
    </>
  )
}

export default ContList