import React from 'react'
import { Container, Flex } from '../../common/common.style'
import { BtnGrp, EmailForm, List, MemberContent } from './member.style'
import TopSec from './TopSec'

const MemberFindId = () => {
  return (
    <>
      <Container>
      <form action='#'>
          <MemberContent className='form'>
            <TopSec title="Login"/>
            <List>
              <EmailForm>
                <Flex className="flex">
                  <input type="email" id="email" className="email" placeholder="이메일 주소" />
                  <select>
                    <option value="">naver.com</option>
                    <option value="">gmail.com</option>
                    <option value="">daum.com</option>
                  </select>
                  <input type="text" id="" className="email_dir" placeholder="직접 입력" />
                </Flex>
              </EmailForm>
              <li className="auth_row">
                <input type="text" id="" className="" placeholder="휴대폰 번호"></input>
                <button>인증번호 발송</button>
              </li>
              <li className="auth_row">
                <input type="text" id="" className="" placeholder="인증번호 입력"></input>
                <button>인증코드 확인</button>
              </li>
            </List>
            <BtnGrp>
              <button>아이디 찾기</button>
            </BtnGrp>
          </MemberContent>
        </form>
      </Container>
    </>
  )
}

export default MemberFindId