import React, { useEffect, useState } from 'react'
import './Header'
import { Link } from 'react-router-dom'
import { HeaderWrap, Logo, HeadList, MbBtn, MenuWrap, MypageLink } from './header.style'
import { Container} from '../common.style'

const Header = () => {
  const [isScrolled, setIsScrolled] = useState(false);
  const [click, setClick] = useState(false)
  const handleClick = () => setClick(!click)
  const closeMobileMenu = () => setClick(false)


  useEffect(() => {
    window.addEventListener('scroll', updateFixed);
    updateFixed();
  }, []); 
  const updateFixed = () => {
    const header = document.querySelector('.header');
    if (header) { 
      if (window.scrollY >1) {
        setIsScrolled(true);
      } else {
        setIsScrolled(false);
      }
    }
  }

  return (
    <HeaderWrap $actBg={isScrolled} className={`header fixed ${isScrolled && 'border_bt'}`}>
      <Container className='container wide'>
          <Logo className='logo' $actType={isScrolled} >
            <Link to="/"><span className="blind">Memorial Canvas</span></Link>
          </Logo>
          <MenuWrap className={`${click ? 'open' : ''}`}>
            <HeadList className="flex nav_area" $act_type={isScrolled}>
              <li><Link to="/" onClick={closeMobileMenu}>HOME</Link></li>
              <li><Link to="/service" onClick={closeMobileMenu}>서비스 소개</Link></li>
              <li><Link to="/dt_notice" onClick={closeMobileMenu}>부고</Link></li>
              <li><Link to="/memorial" onClick={closeMobileMenu}>추모관</Link></li>
              <li><Link to="/faq" onClick={closeMobileMenu}>FAQ</Link></li>
            </HeadList>
            <HeadList className="flex login_area" $act_type={isScrolled}>
              <li><Link to="memberJoin" onClick={closeMobileMenu}>회원가입</Link></li>
              <li><Link to="memberLogin" onClick={closeMobileMenu}>로그인</Link></li>
              <MypageLink $act_type={isScrolled}><Link to="mypage" onClick={closeMobileMenu}><span className='blind'>마이 페이지</span></Link></MypageLink>
            </HeadList>
          </MenuWrap>
          <MbBtn $actType={isScrolled} onClick={handleClick} className={click ? "close" : "menu"}>
            <span className="blind">전체메뉴</span>
          </MbBtn> 
      </Container>
    </HeaderWrap>
  )
}

export default Header