import React from 'react'
import { Swiper, SwiperSlide } from 'swiper/react';
import { Autoplay, Navigation } from 'swiper/modules';
import imgflowr1 from '../../images/bg_flower1.jpg'
import imgflowr2 from '../../images/bg_flower2.jpg'
import imgflowr3 from '../../images/bg_flower3.jpg'
import 'swiper/css';
import { FlowerSlide, SwiperItem } from './deathNotice.style';
const FlowrSlides = () => {
  return (
    <FlowerSlide>
        <Swiper
          slidesPerView={1.4}
          spaceBetween={10}
          navigation={{
            prevEl: ".custom-prev", // 커스텀 prev 버튼
            nextEl: ".custom-next", // 커스텀 next 버튼
          }}
          loop={true}
          modules={[Autoplay, Navigation, Autoplay]}
          autoplay={{
              delay: 3000,
              disableOnInteraction: false,
          }}
          breakpoints={{
            1024: {
              slidesPerView: 3.2,
              spaceBetween: 10,
            },
            768: {
              slidesPerView: 1.4,
            },
           
          }}
        >
          <SwiperSlide>
            <SwiperItem>
              <div className='box'>
                <img src={imgflowr1} />
                <span>삼가 고인의<br />명복을 빕니다.</span>
              </div>
              <div className="from">
                <strong>일산 산악동호회 일동</strong>
                <span className='date'>2022.04.01</span>
              </div>
            </SwiperItem>
          </SwiperSlide>
          <SwiperSlide>
            <SwiperItem>
              <div className='box'>
                <img src={imgflowr1} />
                <span>삼가 고인의<br />명복을 빕니다.</span>
              </div>
              <div className="from">
                <strong>일산 산악동호회 일동</strong>
                <span className='date'>2022.04.01</span>
              </div>
            </SwiperItem>
          </SwiperSlide>
          <SwiperSlide>
            <SwiperItem>
              <div className='box'>
                <img src={imgflowr1} />
                <span>삼가 고인의<br />명복을 빕니다.</span>
              </div>
              <div className="from">
                <strong>일산 산악동호회 일동</strong>
                <span className='date'>2022.04.01</span>
              </div>
            </SwiperItem>
          </SwiperSlide>
          <SwiperSlide>
            <SwiperItem>
              <div className='box'>
                <img src={imgflowr1} />
                <span>삼가 고인의<br />명복을 빕니다.</span>
              </div>
              <div className="from">
                <strong>일산 산악동호회 일동</strong>
                <span className='date'>2022.04.01</span>
              </div>
            </SwiperItem>
          </SwiperSlide>
          <SwiperSlide>
            <SwiperItem>
              <div className='box'>
                <img src={imgflowr1} />
                <span>삼가 고인의<br />명복을 빕니다.</span>
              </div>
              <div className="from">
                <strong>일산 산악동호회 일동</strong>
                <span className='date'>2022.04.01</span>
              </div>
            </SwiperItem>
          </SwiperSlide>
          
        </Swiper>
        <div className="swiper-button-prev custom-prev"></div>
        <div className="swiper-button-next custom-next"></div>
    </FlowerSlide>
  )
}

export default FlowrSlides