import React, { useEffect } from 'react';
// import { BrowserRouter as Router, Route, Routes, useLocation } from 'react-router-dom';
import { HashRouter as Router, Route, Routes, useLocation } from 'react-router-dom';
import Header from './common/header/Header';
import Main from './pages/Main';
import DeathNotice from './pages/DeathNotice';
import Memorial from './pages/Memorial';
import './base.css';
import Footer from './common/footer/Footer';
import Service from './pages/Service';
import Faq from './pages/Faq';
import MemorialRegister from './pages/Memorial/MemorialRegister';
import DtNoticeRegister from './pages/DeathNotice/DtNoticeRegister';
import MemorialView from './pages/Memorial/MemorialView';
import DeathNoticeView from './pages/DeathNotice/DeathNoticeView';
import MemberJoin from './pages/Member/MemberJoin';
import MemberLogin from './pages/Member/MemberLogin';
import MemberFindId from './pages/Member/MemberFindId';
import MemberFindPw from './pages/Member/MemberFindPw';
import Mypage from './pages/Member/Mypage';
import DigitalFlowr from './pages/DeathNotice/DigitalFlowr';
import DigitalFlowerOrder from './pages/DeathNotice/DigitalFlowerOrder';

const ScrollToTop = () => {
  const { pathname } = useLocation();

  useEffect(() => {
    // 페이지 경로가 변경될 때 스크롤을 맨 위로 이동
    window.scrollTo(0, 0);
  }, [pathname]);
  return null;
}
const App = () =>{
  return (
    <>
       <Router>
        <Header />
        <ScrollToTop />
        <Routes>  
          <Route path ="/" element={<Main />} />
          <Route path ="/dt_notice" element={<DeathNotice />} />
          <Route path ="/memorial" element={<Memorial />} />
          <Route path ="/service" element={<Service />} />
          <Route path ="/faq" element={<Faq />} />
          <Route path ="/memorialReg" element={<MemorialRegister />} />
          <Route path ="/deathNoiceReg" element={<DtNoticeRegister />} />
          <Route path ="/memorialView/:id" element={<MemorialView />} />
          <Route path ="/deathNoiceView/:id" element={<DeathNoticeView />} />
          <Route path ="/memberJoin" element={<MemberJoin />} />
          <Route path ="/memberLogin" element={<MemberLogin />} />
          <Route path ="/memberFindId" element={<MemberFindId />} />
          <Route path ="/memberFindPw" element={<MemberFindPw />} />
          <Route path ="/myPage" element={<Mypage />} />
          <Route path ="/digitalFlowr" element={<DigitalFlowr />} />
          <Route path ="/digitalFlowrOrder" element={<DigitalFlowerOrder />} />
        </Routes>
        <Footer />
       </Router>
    </>
  );
}

export default App;
