import React from 'react'
import { SearchForm, TopContent, TopWrap} from './main.style';
import MainTitleImg from '../../images/main/main_title.png';
import VisualSlider from './VisualSlider';
import { Container } from '../../common/common.style';

const MainIntro = () => {
  return (
    <TopWrap>
      {<Container className='wide main_top'>
          <VisualSlider />
          <TopContent>
            <SearchForm className="search_form">
                <form action='#'>
                    <fieldset>
                        <legend>부고검색</legend>
                        <input type="text" className="search_input" placeholder="검색" />
                    </fieldset>
                    <a href="#" className="ico_sch">검색</a>
                </form>
            </SearchForm>
            <div className='letter'><img src={MainTitleImg} alt="With Remembered" /></div>
            <p className="text">사랑하는 고인의 가족, 친지분들에게 부고를 알리며 <br />온라인 장례를 통해 조문을 하실 수 있습니다.</p>
          </TopContent>
      </Container>}
    </TopWrap>
    
  )
}

export default MainIntro