import React from 'react'
import { MajorImgStyle } from './view.style'
import bgFrame from "../../images/frame.png"
import { BgFrame, Img } from '../List/contList.style'
const MajorImg = ({majorImg}) => {
  return (
    <MajorImgStyle>
      <BgFrame>
        <img src={bgFrame} alt="" />
      </BgFrame>
      <Img>
        <img src={majorImg} alt="" />
      </Img>
    </MajorImgStyle>
  )
}

export default MajorImg