import React from 'react'
import { FuneralInfoStyle } from './deathNotice.style'
import MapSample from '../../images/map.jpg';

function FuneralInfo() {
  return (
    <FuneralInfoStyle>
      <ul>
        <li><strong className='title col'>주소</strong>
          <span className='text col'>경기도 용인시 처인구 포곡읍 두계로 72 경기도 용인시 처인구 포곡읍 두계로 72</span>
        </li>
        <li><strong className='title col'>전화번호</strong>
          <span className='text col'>010-1234-5678</span>
        </li>
      </ul>
      <div className="map_area"><img src={MapSample} /></div>
    </FuneralInfoStyle>
  )
}

export default FuneralInfo