import styled from 'styled-components';
import wallet from '../../images/wallet.png'
import flower from '../../images/flower.png'

export const SendStyle = styled.div`
  display:flex;
  gap:2rem;
  .send_others_btn{
    display:flex;
    align-items:center;
  }
  .send_btn{
    font-size:1.8rem;
    font-family: var(--f-second);
    color:var(--primary-color);
    &:before{
      content:"";
      display:inline-block;
      vertical-align:middle;
      margin-right:6px;
      width:2.6rem;
      height:2.4rem;
      background-image:url(${wallet});
      background-size:contain;
      background-repeat:no-repeat;
    }
    &.flower{
      &:before{
        width:2.9rem;
        height:2.4rem;
        background-image:url(${flower});
      }
    }
  }
   @media (max-width: 768px) {
    flex-direction:column;
    align-items:center;
    .send_first_btn{
      width:40vw;
    }
  }
`
export const FlowerSlide = styled.div`
  position:relative;
   --swiper-navigation-color:#535353;  
    --swiper-navigation-size:3.4rem;
  .swiper{
    width:74vw;
    margin:0 auto;
  }
  @media (max-width: 768px) {
    
  }
  
`
export const SwiperItem = styled.div`
  .box{
    position:relative;
    span{
      position:absolute;
      width:100%;
      top:50%;
      left:50%;
      transform:translate(-50%, -50%);
      margin-left:2rem;
      font-size:1.8rem;
      line-height:1.2;
      font-family: var(--f-second);
    } 
  }
  .from{
    margin-top:2rem;
    text-align:left;
    strong{
      display:block;
      margin-bottom:1rem;
      font-size:1.6rem;
      color:#0C1622;
      font-weight:normal;
    }
    span{
      display:block;
      font-size:1.4rem;
      color:var(--gray-color);
    }
  }
 
`
export const FuneralInfoStyle = styled.div`
  ul li{
    display:table;
    line-height:var(--txt-lh);
    .col{
      display:table-cell;   
      &.title{
        width:10rem;  
        font-size:1.8rem;
      }
      &.text{
        color:var(--gray-lg-dk-color);
        font-size:1.6rem;
      }
    }
    strong{
    }
  }
  .map_area{
    margin-top:3rem;
  }
`
